import React, { useCallback } from 'react';
import { Doughnut } from 'react-chartjs-2';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import Cookies from 'js-cookie';
import { Link } from 'react-router-dom';
import { MagnifyingGlassIcon, ReceiptPercentIcon } from '@heroicons/react/24/solid';

ChartJS.register(ArcElement, Tooltip, Legend);




const Bills = () => {
    document.title = "BILLING"

    const [order, setOrder] = React.useState(0)
    const [bills, setBills] = React.useState([]);
    const [displayBills, setDisplayBills] = React.useState([]);
    const [paidBills, setPaidBills] = React.useState([]);
    const [unpaidBills, setUnpaidBills] = React.useState([]);
    const [refundBills, setRefundBills] = React.useState([]);
    const [amount, setAmount] = React.useState({ total: 0, paid: 0, unpaid: 0, refund: 0 })

    const [search, setSearch] = React.useState('')


    React.useEffect(() => {
        switch (order) {
            case 0:
                setDisplayBills(bills);
                break;
            case 1:
                setDisplayBills(paidBills);
                break;
            case 2:
                setDisplayBills(unpaidBills);
                break;
            default:
                console.error('Invalid order value:', order);
        }
    }, [order, bills, paidBills, unpaidBills]);


    React.useEffect(() => {
        const filtered = bills.filter(bill => {
            const billIdMatch = bill.bill_id.toString().includes(search);
            const studentNameMatch = typeof bill.first_name === 'string' && bill.first_name.toLowerCase().includes(search.toLowerCase());
            const studentIdMatch = bill.student_id.toString().includes(search);
            return billIdMatch || studentIdMatch || studentNameMatch;
        });
        setDisplayBills(filtered);
    }, [search, bills]);

    const fetchBills = useCallback(async () => {

        const token = Cookies.get("authToken");
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/bill`, {
                method: 'GET',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                }
            })

            if (network.ok) {
                let result = await network.json();
                setBills(result);
                setDisplayBills(result);
                const paid = result.filter(bill => bill.bill_status === 'Paid');
                const unpaid = result.filter(bill => bill.bill_status === 'Unpaid');
                const refund = result.filter(bill => bill.bill_status === 'Refund');
                setPaidBills(paid);
                setUnpaidBills(unpaid);
                setRefundBills(refund)
                const totalPaidAmount = paid.reduce((acc, bill) => {
                    return acc + parseFloat(bill.bill_amount);  // Convert the bill amount to float and sum
                }, 0);
                const totalUnPaidAmount = unpaid.reduce((acc, bill) => {
                    return acc + parseFloat(bill.bill_amount);  // Convert the bill amount to float and sum
                }, 0);
                const totalRefundAmount = refund.reduce((acc, bill) => {
                    return acc + parseFloat(bill.bill_amount);  // Convert the bill amount to float and sum
                }, 0);
                const totalAmount = totalPaidAmount + totalUnPaidAmount
                setAmount({ total: totalAmount, paid: totalPaidAmount, unpaid: totalUnPaidAmount, refund: totalRefundAmount })
            } else {
                await network.text();
            }
        } catch (error) {

        }
    }, [])

    React.useEffect(() => {
        fetchBills()
    }, [fetchBills])


    // Data for the doughnut chart
    const data = {
        labels: ['Paid', 'Unpaid', 'Refund'],
        datasets: [
            {
                data: [paidBills.length, unpaidBills.length, refundBills.length],  // The numbers representing each invoice type
                backgroundColor: ['#28a745', '#dc3545', '#eab308'],  // Colors for each section
                borderColor: ['#28a745', '#dc3545', '#eab308'],
                borderWidth: 1,
                cutout: '70%',  // To make the doughnut thinner
            },
        ],
    };

    const amountData = {
        labels: ['Paid', 'Unpaid', 'Refund'],
        datasets: [
            {
                data: [amount.paid, amount.unpaid, amount.refund],  // The numbers representing each invoice type
                backgroundColor: ['#28a745', '#dc3545', '#eab308'],  // Colors for each section
                borderColor: ['#28a745', '#dc3545', '#eab308'],
                borderWidth: 1,
                cutout: '70%',  // To make the doughnut thinner
            },
        ],
    };

    // Custom options for the chart
    const options = {
        plugins: {
            legend: {
                display: false,  // Hide the default legend
            },
            tooltip: {
                enabled: false,  // Disable the tooltip since we have custom labels
            },
        },
        responsive: true,
        maintainAspectRatio: false,
    };


    return (
        <>
            <div className="flex flex-wrap">
                <div className='w-full lg:w-1/2 p-4'>
                    <div className='flex justify-center items-center h-full w-full p-4 shadow rounded bg-base-300'>
                        <div className="relative w-48 h-48">
                            <Doughnut data={data} options={options} />
                            {/* Center text inside the doughnut */}
                            <div className="absolute inset-0 flex flex-col justify-center items-center">
                                <p className="text-2xl font-bold">{bills.length}</p>
                                <p className="text-sm">Total Invoices</p>
                            </div>
                        </div>
                        {/* Labels next to the chart */}
                        <div className="ml-8 flex flex-col justify-center">
                            <div className="flex items-center mb-2">
                                <span className="w-4 h-4 inline-block mr-2 rounded-full bg-green-500"></span>
                                <p className="text-sm ">{paidBills.length} Paid</p>
                            </div>
                            <div className="flex items-center mb-2">
                                <span className="w-4 h-4 inline-block mr-2 rounded-full bg-red-500"></span>
                                <p className="text-sm ">{unpaidBills.length} Unpaid</p>
                            </div>
                            <div className="flex items-center mb-2">
                                <span className="w-4 h-4 inline-block mr-2 rounded-full bg-yellow-500"></span>
                                <p className="text-sm ">{refundBills.length} Refund</p>
                            </div>
                        </div>
                    </div>
                </div>

                <div className='w-full lg:w-1/2 p-4'>
                    <div className='flex justify-center items-center h-full w-full p-4 shadow rounded bg-base-300'>
                        <div className="relative w-48 h-48">
                            <Doughnut data={amountData} options={options} />
                            {/* Center text inside the doughnut */}
                            <div className="absolute inset-0 flex flex-col justify-center items-center">
                                <p className="text-2xl font-bold"> {amount.total}</p>
                                <p className="text-sm">Total Amount</p>
                            </div>
                        </div>
                        {/* Labels next to the chart */}
                        <div className="ml-8 flex flex-col justify-center">
                            <div className="flex items-center mb-2">
                                <span className="w-4 h-4 inline-block mr-2 rounded-full bg-green-500"></span>
                                <p className="text-sm "> {amount.paid} Received</p>
                            </div>
                            <div className="flex items-center mb-2">
                                <span className="w-4 h-4 inline-block mr-2 rounded-full bg-red-500"></span>
                                <p className="text-sm "> {amount.unpaid} Unpaid</p>
                            </div>
                            <div className="flex items-center mb-2">
                                <span className="w-4 h-4 inline-block mr-2 rounded-full bg-yellow-500"></span>
                                <p className="text-sm "> {amount.refund} Refund</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="p-4 min-h-screen">
                <div className="bg-base-300 p-4 rounded shadow-md">
                    <div className="flex justify-between items-center border-b border-base-100">
                        <h1 className="text-xl font-bold mb-2">Recent Invoices</h1>
                    </div>

                    {/** Search and filter */}
                    <div className="mt-4 flex justify-between items-center">
                        <label className="input rounded flex items-center gap-2">
                            <input
                                type="text"
                                value={search}
                                onChange={(e) => setSearch(e.target.value)}
                                className="grow"
                                placeholder="Invoice no, Student ID" />
                            <MagnifyingGlassIcon className='size-6' />
                        </label>


                        {/** List Box */}

                        <div className="w-52">
                            <select value={order} onChange={(e) => setOrder(Number(e.target.value))} className="select select-bordered w-full max-w-xs">
                                <option value={0}>All</option>
                                <option value={1}>Paid</option>
                                <option value={2}>Unpaid</option>
                            </select>
                        </div>
                    </div>

                    {/** INVOICE TABLE */}

                    <div className="mt-4 overflow-x-auto">
                        <table className="table">
                            <thead className="bg-base-100">
                                <tr>
                                    <th className="text-left py-3 px-4">Invoice Details</th>
                                    <th className="text-left py-3 px-4">Amount</th>
                                    <th className="text-left py-3 px-4">Bill Date</th>
                                    <th className="text-left py-3 px-4">Status</th>
                                    <th className="text-left py-3 px-4">Action</th>
                                </tr>
                            </thead>
                            <tbody>
                                {displayBills.map((invoice, index) => (
                                    <tr key={index} className="">
                                        <td className="py-3 px-4">
                                            <div>{invoice.student_name}</div>
                                            <div className="text-gray-500 text-sm">#{invoice.bill_id}</div>
                                        </td>
                                        <td className="py-3 px-4">
                                            <div> {invoice.bill_amount}</div>
                                        </td>
                                        <td className="py-3 px-4">{invoice.bill_date.split("T")[0]}</td>
                                        <td className="py-3 px-4">
                                            <span className={`badge ${invoice.bill_status === 'Paid' ? 'badge-success' : invoice.bill_status === 'Refund' ? 'badge-warning' : 'badge-error'}`}>
                                                {invoice.bill_status}
                                            </span>
                                        </td>
                                        <td className="py-3 px-4">
                                            <Link className='btn btn-primary btn-sm rounded' to={`${invoice.bill_id}`}>
                                                <ReceiptPercentIcon className='me-2 h-6 w-6' /> View Invoice
                                            </Link>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Bills