import { DocumentTextIcon, PlusIcon } from '@heroicons/react/24/solid'
import React, { useCallback, useRef, useState } from 'react'
import Exam from './components/Exam'
import { useAlertContext } from '../../context/AlertContext'
import Cookies from 'js-cookie'

const Exams = () => {
    const { showLoading, showToast } = useAlertContext()
    const [exams, setExams] = useState([])
    const newExamModal = useRef()
    const newExamModalCloseForm = useRef()
    const [newExamName, setNewExamName] = useState("")



    const fetchExams = useCallback(async () => {
        showLoading(true)
        const token = Cookies.get("authToken");
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/exam/all`, {
                method: 'GET',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
            })
            if (network.ok) {
                let data = await network.json()
                setExams(data)
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }
    }, [showLoading, showToast])

    React.useEffect(() => {
        fetchExams()
    }, [])

    const createExam = async () => {
        const token = Cookies.get("authToken");
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/exam`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ name: newExamName })
            })
            if (network.ok) {
                newExamModalCloseForm.current.submit()
                fetchExams()
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }
    }

    const updateExam = async (status, exam) => {
        const token = Cookies.get("authToken");
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/exam/${exam.exam_id}`, {
                method: 'PATCH',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ status: status })
            })
            if (network.ok) {
                showToast("Exam updated !", "success")
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }
    }

    return (
        <>
            <div className='flex justify-between items-center px-4 mt-4'>
                <label className='font-bold text-2xl flex items-center gap-2'>
                    <DocumentTextIcon className='size-6' /> Exam
                </label>
                <button className='btn btn-secondary rounded' onClick={() => newExamModal.current.showModal()}>
                    <PlusIcon className='size-6' />
                    Add Exam
                </button>
            </div>

            <div className="divider"></div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 p-6">
                {
                    exams.map((exam, index) => (
                        <Exam key={index} exam={exam} onUpdate={updateExam} />
                    ))
                }
            </div>

            <dialog ref={newExamModal} className="modal">
                <div className="modal-box">
                    <form method="dialog" ref={newExamModalCloseForm}>
                        {/* if there is a button in form, it will close the modal */}
                        <button className="btn btn-sm btn-circle btn-ghost absolute right-2 top-2"></button>
                    </form>

                    <div className='space-y-4'>
                        <h3 className="font-bold text-lg">New Exam</h3>
                        <input type="text" value={newExamName} onChange={(e) => setNewExamName(e.target.value)} placeholder="Type here" className="input input-bordered rounded focus:outline-none w-full" />
                    </div>

                    <div className='flex justify-end mt-8'>
                        <button className='btn btn-success rounded' onClick={createExam}>
                            <PlusIcon className='size-6' /> Create
                        </button>
                    </div>
                </div>
            </dialog>
        </>
    )
}

export default Exams