import { InformationCircleIcon, PlusIcon } from '@heroicons/react/24/solid'
import React, { useEffect, useState } from 'react'
import Cookies from 'js-cookie'
import { Link } from 'react-router-dom'
import { useAlertContext } from '../../context/AlertContext'


const AdminEnquiries = () => {
    const { showToast, showLoading } = useAlertContext()
    const [drafts, setDrafts] = useState([])

    useEffect(() => {
        ; (async () => {
            const token = Cookies.get("authToken");
            try {
                showLoading(true)

                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/student/draft`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    }
                })

                if (network.ok) {
                    let data = await network.json()
                    setDrafts(data);
                } else {
                    const errorText = await network.text();
                    showToast(errorText, "error")
                }
            } catch (error) {
                showToast(error.message, "error")
            } finally {
                showLoading(false)
            }

        })()
    }, [])


    return (
        <>
            <div className='flex justify-between items-center px-4 mt-4'>
                <label className='font-bold text-2xl flex items-center gap-2'>
                    <InformationCircleIcon className='size-6' /> Enquery
                </label>
                <Link className='btn btn-secondary rounded' to={'new'}>
                    <PlusIcon className='size-6' />
                    New enquiry
                </Link>
            </div>

            <div className='divider'></div>

            <div className='m-10 bg-base-300 rounded'>
                <div className="overflow-x-auto">
                    <table className="table">
                        {/* head */}
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th>Name</th>
                                <th>phone</th>
                                <th>Parent name</th>
                                <th>Address</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                drafts.map((student, index) => (
                                    <tr key={index}>
                                        <th><Link to={`/admin/student/${student.student_id}`} className='link'>{student.student_id}</Link></th>
                                        <th>{student.student_name}</th>
                                        <th>{student.phone}</th>
                                        <th>{student.parent_name}</th>
                                        <th>{student.address}</th>
                                    </tr>
                                ))
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        </>
    )
}

export default AdminEnquiries