import React from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import Cookies from 'js-cookie';
import IDCard from '../../common/ui/IDCard';
import { CheckIcon } from '@heroicons/react/24/solid';
import { useAlertContext } from '../../context/AlertContext';


const StudentInfo = () => {
    document.title = "STUDENT PROFILE"

    const { showLoading, showToast } = useAlertContext()
    const navigate = useNavigate()
    const { id } = useParams()
    const [sessions, setSessions] = React.useState([])
    const [classes, setClasses] = React.useState([])
    const [selectedSession, setSelectedSession] = React.useState(0)
    const [selectedClass, setSelectedClass] = React.useState(0)
    const [formData, setFormData] = React.useState({
        student_name: '',
        parent_name: '',
        dob: '',
        phone: '',
        address: '',
        gender: ''
    });


    React.useEffect(() => {
        ; (async () => {
            showLoading(true)
            const token = Cookies.get('authToken');
            try {
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/student/info/${id}`, {
                    method: 'GET',
                    headers: {
                        Authorization: `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                });

                if (network.ok) {
                    let data = await network.json();
                    setFormData(data);
                } else {
                    const errorText = await network.text();
                    console.error(errorText);
                }
            } catch (error) {
                console.error(error);
            }
            try {
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/session/admission`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                })
                if (network.ok) {
                    let data = await network.json()
                    setSessions(data)
                } else {
                    const errorText = await network.text();
                    console.log(errorText)
                }
            } catch (error) {
                console.error(error)
            } finally {
                showLoading(false)
            }

            try {
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/class`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                })
                if (network.ok) {
                    let data = await network.json()
                    setClasses(data)
                } else {
                    const errorText = await network.text();
                    console.log(errorText)
                }
            } catch (error) {
                console.log(error)
            } finally {
                showLoading(false)
            }
        })()
    }, [id])




    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
    };


    const handleSubmit = async (e) => {
        e.preventDefault();
        showLoading(true)
        const token = Cookies.get("authToken");

        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/student/info/${id}`, {
                method: 'PUT',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(formData)
            })

            if (!network.ok) {
                const errorText = await network.text();
                showToast(errorText, "error")
            } else {
                showToast("Student data updated", "success")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }
    };

    const promote = async () => {
        const token = Cookies.get("authToken");
        if (selectedClass === 0 || selectedSession === 0) {
            return
        }

        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/student/promote`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    student_id: id, class_id: selectedClass, session_id: selectedSession
                })
            })

            if (network.ok) {
                let data = await network.json()
                if (data.bill !== 0) {
                    navigate('/admin/bills/' + data.bill)
                } else {
                    showToast("Already promoted !", "success", 5000)
                }

            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showLoading(false)
            showToast(error.message, "error")
        }
        showLoading(false)
    }

    return (
        <>
            <div className='flex flex-wrap'>
                <div className='w-full lg:w-2/3 p-4'>
                    <div className="p-4 bg-base-300 rounded shadow-md flex flex-wrap">
                        <div className="w-full flex flex-col lg:w-1/3 p-4">
                            <label htmlFor="session" className="mb-2">Session</label>
                            <select onChange={(e) => setSelectedSession(Number(e.target.value))} className='select select-primary rounded-none focus:outline-none w-full'>
                                <option disabled selected>Select session</option>
                                {
                                    sessions.map((session) => (
                                        <option value={session.session_id} key={session.session_id}>{session.start} - {session.end}</option>
                                    ))
                                }
                            </select>
                        </div>
                        <div className="w-full flex flex-col lg:w-1/3 p-4">
                            <label htmlFor="class" className="mb-2">Class</label>
                            <select value={selectedClass} onChange={(e) => setSelectedClass(Number(e.target.value))} className="select select-info w-full rounded-none">
                                <option value={0} disabled selected>Select class</option>
                                {
                                    classes.map((cls) => (
                                        <option value={cls.class_id} key={cls.class_id}>{cls.class_name}</option>
                                    ))
                                }
                            </select>
                        </div>
                        <div className="w-full flex flex-col lg:w-1/3 p-4">
                            <label htmlFor="class" className="mb-2">Action</label>
                            <button className="btn btn-success rounded w-full" onClick={promote}>
                                Promote
                            </button>
                        </div>

                    </div>

                    {/** Form */}
                    <div className="p-6 mt-6 bg-base-300 rounded shadow-md">
                        <form onSubmit={handleSubmit}>
                            <h2 className='text-2xl text-center'>STUDENT INFO</h2>

                            <div className='divider'></div>

                            <div className='flex flex-wrap'>

                                <div className='w-1/2 p-4 space-y-2'>
                                    <div >
                                        <label className="block">Student name</label>
                                        <input
                                            type="text"
                                            name="student_name"
                                            autoComplete='given-name'
                                            value={formData.student_name}
                                            onChange={handleChange}
                                            className={'input rounded focus:outline-none w-full'}
                                            required
                                            placeholder='First name'
                                        />
                                    </div>

                                    <div >
                                        <label className="block text-lg">Parent Name</label>
                                        <input
                                            type="text"
                                            name="parent_name"
                                            value={formData.parent_name}
                                            onChange={handleChange}
                                            className={'input rounded focus:outline-none w-full'}
                                            required
                                            placeholder='Name of the parent_name'
                                        />
                                    </div>

                                    <div >
                                        <label className="block text-lg">Date of birth</label>
                                        <input
                                            type="date"
                                            name="dob"
                                            value={formData.dob}
                                            onChange={handleChange}
                                            className={'input rounded focus:outline-none w-full'}
                                            placeholder='DOB' // Assuming kindergarten dobs
                                        />
                                    </div>
                                </div>
                                <div className='w-1/2 p-4 space-y-2'>
                                    <div>
                                        <label className="block text-lg">Gender</label>
                                        <select
                                            value={formData.gender}
                                            name='gender'
                                            onChange={handleChange}
                                            className={'input rounded focus:outline-none w-full'}
                                            required
                                        >
                                            <option>Select gender</option>
                                            <option value="male" defaultChecked>Male</option>
                                            <option value="female">Female</option>
                                        </select>
                                    </div>

                                    <div >
                                        <label className="block text-lg">Contact number</label>
                                        <input
                                            type="tel"
                                            name="phone"
                                            value={formData.phone}
                                            onChange={handleChange}
                                            autoComplete='tel-national'
                                            className={'input rounded focus:outline-none w-full'}
                                            required
                                            placeholder='Phone number'
                                        />
                                    </div>

                                    <div >
                                        <label className="block text-lg">Address</label>
                                        <textarea
                                            name="address"
                                            value={formData.address}
                                            onChange={handleChange}
                                            className={'input rounded focus:outline-none w-full'}
                                            rows="3"
                                        />
                                    </div>
                                </div>

                            </div>





                            <div className='flex justify-end'>
                                <button type="submit" className="btn btn-success rounded">
                                    <CheckIcon className='size-6' /> Save
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
                <div className='w-full lg:w-1/3 p-4'>
                    <IDCard id={id} name={formData.student_name} phone={formData.phone} school={formData.school_name} photo={formData.photo} type={'Student'} />

                </div>
            </div>
        </>
    );
}

export default StudentInfo