import React from 'react'
import Cookies from 'js-cookie';
import { useAlertContext } from '../../context/AlertContext';
import { useNavigate } from 'react-router-dom';


const NewEnquery = () => {
    const { showLoading, showToast } = useAlertContext()
    const navigate = useNavigate()

    const [formData, setFormData] = React.useState({
        student_name: '',
        parent: '',
        dob: '',
        phone: '',
        address: '',
        gender: 'male'
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
    };


    const handleSubmit = async (e) => {
        e.preventDefault();
        showLoading(true)
        const token = Cookies.get("authToken");

        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/student/draft`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(formData)
            })

            if (network.ok) {
                showToast("Enqury added !", "success")
                navigate("/admin/enquiries")
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }
    };




    return (
        <>
            <div className="p-6 m-16 rounded shadow-md bg-base-300">
                <form onSubmit={handleSubmit}>
                    <h2 className="text-2xl text-center">Inquiry Form</h2>
                    <div className='divider'></div>


                    <div className="flex flex-wrap">
                        {/* Left Column */}
                        <div className="md:w-1/2 p-4 space-y-4">
                            <div className="form-control">
                                <label className="label">
                                    <span className="label-text">Student's Name</span>
                                </label>
                                <input
                                    type="text"
                                    name="student_name"
                                    autoComplete="given-name"
                                    value={formData.student_name}
                                    onChange={handleChange}
                                    className="input input-bordered w-full"
                                    placeholder="Enter student's name"
                                    required
                                />
                            </div>

                            <div className="form-control">
                                <label className="label">
                                    <span className="label-text">Parent's Name</span>
                                </label>
                                <input
                                    type="text"
                                    name="parent"
                                    value={formData.parent}
                                    onChange={handleChange}
                                    className="input input-bordered w-full"
                                    placeholder="Enter parent's name"
                                    required
                                />
                            </div>

                            <div className="form-control">
                                <label className="label">
                                    <span className="label-text">Date of Birth</span>
                                </label>
                                <input
                                    type="date"
                                    name="dob"
                                    value={formData.dob}
                                    onChange={handleChange}
                                    className="input input-bordered w-full"
                                    required
                                />
                            </div>
                        </div>

                        {/* Right Column */}
                        <div className="md:w-1/2 p-4 space-y-4">
                            <div className="form-control">
                                <label className="label">
                                    <span className="label-text">Gender</span>
                                </label>
                                <select
                                    name="gender"
                                    value={formData.gender}
                                    onChange={handleChange}
                                    className="select select-bordered w-full"
                                    required
                                >
                                    <option disabled value="">
                                        Select gender
                                    </option>
                                    <option value="male">Male</option>
                                    <option value="female">Female</option>
                                </select>
                            </div>

                            <div className="form-control">
                                <label className="label">
                                    <span className="label-text">Contact Number</span>
                                </label>
                                <input
                                    type="tel"
                                    name="phone"
                                    value={formData.phone}
                                    onChange={handleChange}
                                    autoComplete="tel-national"
                                    className="input input-bordered w-full"
                                    placeholder="Enter phone number"
                                    required
                                />
                            </div>

                            <div className="form-control">
                                <label className="label">
                                    <span className="label-text">Address</span>
                                </label>
                                <textarea
                                    name="address"
                                    value={formData.address}
                                    onChange={handleChange}
                                    className="textarea textarea-bordered w-full"
                                    rows="3"
                                    placeholder="Enter address"
                                    required
                                ></textarea>
                            </div>
                        </div>
                    </div>


                    <div className='flex justify-end'>
                        <button type="submit" className="btn btn-primary rounded">
                            Submit
                        </button>
                    </div>
                </form>
            </div>

        </>
    )
}

export default NewEnquery