import React from 'react'

const Exam = ({ exam, onUpdate }) => {

    const [statusSwitch, setStatus] = React.useState(false)

    React.useEffect(() => {
        setStatus((exam.exam_status === 1) ? true : false)
    }, [exam.exam_status])


    const changeStatus = async () => {
        setStatus(!statusSwitch)
        onUpdate(!statusSwitch, exam)
    }

    return (
        <div className="bg-base-300 rounded-md p-6 space-y-4">
            <span className="text-2xl block font-bold">{exam.exam_name}</span>

            <div className="form-control w-full">
                <label className="label cursor-pointer">
                    <span className="label-text">Active status</span>
                    <input type="checkbox" className="toggle toggle-accent" checked={statusSwitch}
                        onChange={changeStatus} />
                </label>
            </div>
        </div>
    )
}

export default Exam