import { BrowserRouter, Routes, Route } from "react-router-dom";
import './App.css';
import { useAuth } from './context/AuthContext';
import Splash from "./common/ui/Splash";
import LoginPage from "./routes/login/LoginPage";
import AdminLayout from "./layout/AdminLayout";
import AdminDashboard from "./routes/admin/AdminDashboard";
import AdminInventory from "./routes/admin/AdminInventory";
import Pos from "./routes/billing/Pos";
import Bills from "./routes/billing/Bills";
import Bill from "./routes/billing/Bill";
import AdminSchool from "./routes/admin/AdminSchool";
import AdminSession from "./routes/admin/AdminSession";
import AdminClass from "./routes/admin/AdminClass";
import AdminEnquiries from "./routes/admin/AdminEnquiries";
import NewEnquery from "./routes/admin/NewEnquery";
import AdminAdmission from "./routes/admin/AdminAdmission";
import FindStudent from "./routes/student/FindStudent";
import StudentInfo from "./routes/student/StudentInfo";
import AdminStaffs from "./routes/admin/AdminStaffs";
import StaffForm from "./routes/admin/StaffForm";
import Exams from "./routes/admin/Exams";
import Marks from "./routes/admin/Marks";
import AdminResult from "./routes/admin/AdminResult";
import Marksheet from "./routes/Marksheet";
import Home from "./routes/Home";
import TeacherLayout from "./layout/TeacherLayout";



function App() {

  const { isAuthenticated, loading } = useAuth();

  if (loading) {
    return (
      <div className="h-screen flex justify-center">
        <Splash />
      </div>)
  }



  return (
    <BrowserRouter>
      <Routes>
        {
          isAuthenticated ? (
            <>
              <Route path="/" element={<Home />} />

              <Route path="/admin" element={<AdminLayout />}>
                <Route path="" element={<AdminDashboard />} />
                <Route path="school" element={<AdminSchool />} />
                <Route path="session" element={<AdminSession />} />
                <Route path="class" element={<AdminClass />} />
                <Route path="enquiries" element={<AdminEnquiries />} />
                <Route path="enquiries/new" element={<NewEnquery />} />
                <Route path="admission" element={<AdminAdmission />} />
                <Route path="student" element={<FindStudent />} />
                <Route path="student/:id" element={<StudentInfo />} />

                <Route path="staffs" element={<AdminStaffs />} />
                <Route path="staffs/new" element={<StaffForm />} />

                <Route path="exams" element={<Exams />} />
                <Route path="marks" element={<Marks />} />
                <Route path="result" element={<AdminResult />} />
                <Route path="inventory" element={<AdminInventory />} />
                <Route path="pos" element={<Pos />} />
                <Route path="bills" element={<Bills />} />
                <Route path="bills/:id" element={<Bill />} />
              </Route>

              <Route path="/teacher" element={<TeacherLayout />}>
                <Route path="" element={<Marks />} />
                <Route path="marks" element={<Marks />} />
              </Route>



              <Route path="/marksheet/:student/:session/:classId/:exam" element={<Marksheet />}>

              </Route>
            </>
          ) : (

            <>
              <Route path='/' element={<LoginPage />} />
              <Route path='/login' element={<LoginPage />} />
            </>)
        }
      </Routes>
    </BrowserRouter>
  );
}

export default App;
