import { MagnifyingGlassIcon, UserPlusIcon, UsersIcon } from '@heroicons/react/24/solid'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { useAlertContext } from '../../context/AlertContext'
import Cookies from 'js-cookie'

const AdminStaffs = () => {
    const { showLoading, showToast } = useAlertContext()
    const [staffs, setStaffs] = useState([])
    const [searchQuery, setSearchQuery] = useState("")


    useEffect(() => {
        ; (async () => {
            showLoading(true)
            const token = Cookies.get("authToken");
            try {
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/staff`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                })
                if (network.ok) {
                    let data = await network.json()
                    setStaffs(data)
                } else {
                    const errorText = await network.text();
                    showToast(errorText, "error")
                }
            } catch (error) {
                showToast(error.message, "error")
            } finally {
                showLoading(false)
            }
        })()
    }, [])



    return (
        <>
            <div className='p-4 flex justify-between items-center'>
                <label className='font-bold text-2xl flex items-center gap-4'>
                    <UsersIcon className='size-6' /> Staffs

                    <Link to={'new'} className='btn btn-success rounded'>
                        <UserPlusIcon className='size-6' /> Add new staff
                    </Link>
                </label>
                <label className="input input-bordered flex items-center gap-2">
                    <input type="text" value={searchQuery} onChange={(e) => setSearchQuery(e.target.value)} className="grow" placeholder="Search" />
                    <MagnifyingGlassIcon className='size-4' />
                </label>
            </div>

            <div className='divider mt-0'></div>


            <div className='p-4'>
                <table className="w-full table border border-base-300">
                    <thead>
                        <tr>
                            <th className="py-2 px-4 text-left font-semibold ">Name</th>
                            <th className="py-2 px-4 text-left font-semibold ">Staff ID</th>
                            <th className="py-2 px-4 text-left font-semibold ">Phone</th>
                            <th className="py-2 px-4 text-left font-semibold ">Role</th>
                            <th className="py-2 px-4 text-left font-semibold ">Gender</th>
                        </tr>
                    </thead>
                    <tbody className='divide-y divide-base-300'>
                        {staffs.map((staff, index) => (
                            <tr key={index}>
                                <td className="py-2 px-4">
                                    <div className="flex items-center space-x-3">
                                        <img
                                            src={staff.staff_image === null ? "/favicon.ico" : staff.staff_image}
                                            alt={`${staff.staff_name}`}
                                            className="w-8 h-8 rounded-full"
                                        />
                                        <span><Link className='hover:underline' to={`${staff.staff_id}`}>{`${staff.staff_name}`}</Link> </span>
                                    </div>
                                </td>
                                <td className="py-2 px-4">{staff.staff_id}</td>
                                <td className="py-2 px-4">{staff.staff_phone}</td>
                                <td className="py-2 px-4">{staff.role}</td>
                                <td className="py-2 px-4 uppercase">{staff.staff_gender}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </>
    )
}

export default AdminStaffs