import { CheckCircleIcon, ExclamationCircleIcon, ExclamationTriangleIcon } from "@heroicons/react/24/solid";
import React, { createContext, useState, useContext } from "react";

const AlertContext = createContext();

export const useAlertContext = () => {
    return useContext(AlertContext);
};

export const AlertContextProvider = ({ children }) => {
    const [toast, setToast] = useState({ visible: false, message: "", type: "info" });
    const [loading, setLoading] = useState(false);

    // Toast display logic with configurable duration
    const showToast = (message, type = "info", duration = 2000) => {
        setToast({ visible: true, message, type });
        setTimeout(() => setToast({ visible: false, message: "", type: "info" }), duration);
    };

    const showLoading = (state) => {
        setLoading(state);
    };

    // Toast type-to-class mapping for better readability
    const toastClasses = {
        info: "alert-info",
        success: "alert-success",
        error: "alert-error",
        warning: "alert-warning"
    };

    const toastIcons = {
        success: <CheckCircleIcon className="size-6" />,
        error: <ExclamationCircleIcon className="size-6" />,
        info: <CheckCircleIcon className="size-6" />, // Fallback icon for info
        warning: <ExclamationTriangleIcon className="size-6" />,
    };

    return (
        <AlertContext.Provider value={{ showToast, showLoading }}>
            {/* Loading Bar */}
            <div
                className={`fixed top-0 left-0 right-0 h-1 z-20 bg-blue-500 transition-transform duration-300 ${loading ? "translate-y-0" : "-translate-y-full"
                    }`}
            >
                <div
                    className={`h-full bg-green-500 transition-width duration-300 ${loading ? "loading-animation" : ""
                        }`}
                    style={{ width: loading ? "100%" : "0%" }}
                ></div>
            </div>

            {/* Render Children */}
            {children}

            {/* Toast */}
            {toast.visible && (
                <div className="toast z-50">
                    <div
                        className={`rounded alert ${toastClasses[toast.type]}`}
                        role="alert"
                    >
                        {toastIcons[toast.type]}
                        <span className="ml-2">{toast.message}</span>
                    </div>
                </div>
            )}
        </AlertContext.Provider>
    );
};