import { TrashIcon } from '@heroicons/react/24/solid';
import React from 'react';


const Subject = ({ label, id, onRemove }) => {
    return (
        <div className="w-full p-2 bg-base-200 flex justify-between items-center">
            <span className="text-lg truncate w-3/4">
                {label}
            </span>
            <button
                onClick={() => onRemove(id)}
                className="ml-4 px-3 py-1 text-white bg-red-500 rounded-md hover:bg-red-600 focus:outline-none focus:bg-red-600"
            >
                <TrashIcon className='h-6 w-6' />
            </button>
        </div>
    );
};

export default Subject;
