
import React, { useCallback } from 'react'
import Cookies from 'js-cookie'
import PosItem from './components/PosItem';
import CartItem from './components/CartItem';
import { useNavigate } from 'react-router-dom';
import { UserCircleIcon } from '@heroicons/react/24/solid';
import { useAlertContext } from '../../context/AlertContext';


const Pos = () => {
    document.title = "POS";
    const { showLoading } = useAlertContext()
    const navigate = useNavigate();
    const [student, setStudent] = React.useState({ student_id: 0, first_name: "No student selected" })
    const [data, setData] = React.useState([])
    const [studentId, setStudentId] = React.useState('')
    const [cart, setCart] = React.useState([])

    const fetchInventories = useCallback(async () => {
        showLoading(true)
        const token = Cookies.get("authToken");
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/inventory`, {
                method: 'GET',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
            })
            if (network.ok) {
                let data = await network.json()
                setData(data)
            }
        } catch (error) {

        } finally {
            showLoading(false)
        }
    }, [])


    const loadStudent = async () => {
        showLoading(true)
        const token = Cookies.get("authToken");
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/student/info/${studentId}`, {
                method: 'GET',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
            })

            if (network.ok) {
                let data = await network.json()
                setStudent(data)
                fetchInventories()
            } else {
                const errorText = await network.text();
                setStudent({ ...student, student_id: 0, first_name: errorText })
            }
        } catch (error) {
            setStudent({ ...student, student_id: 0, first_name: 'Check network !' })
        } finally {
            showLoading(false)
        }
    }

    const addItem = (item) => {
        setCart((prev) => [...prev, { id: item.item_id, name: item.item_name, price: item.item_price, count: 1, stock: item.item_stock }])
    }

    const updateItemCount = (index, newCount) => {
        setCart((prevCart) => {
            // If the new count is 0, remove the item from the cart
            if (newCount === 0) {
                return prevCart.filter((_, i) => i !== index);
            }

            // Otherwise, update the count
            return prevCart.map((item, i) =>
                i === index ? { ...item, count: newCount } : item
            );
        });
    };

    const removeItem = (index) => {
        setCart((prevCart) => prevCart.filter((_, i) => i !== index));
    };

    const totalAmount = cart.reduce((sum, item) => sum + (item.price * item.count), 0);


    const createBill = async () => {
        showLoading(true)
        const token = Cookies.get("authToken");
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/bill`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ student_id: studentId, items: cart })
            })

            if (network.ok) {
                let data = await network.json()
                navigate(`/admin/bills/${data.bill}`)
            }
        } catch (error) {

        } finally {
            showLoading(false)
        }
    }

    return (
        <>
            {/** Top Nav for Selecting class and add class */}
            <nav className='p-2 w-full border-b border-base-300 flex justify-between items-center'>
                <label className="input input-bordered flex items-center gap-2">
                    <UserCircleIcon className='size-4' />
                    <input type="text"
                        value={studentId}
                        onChange={(e) => setStudentId(e.target.value)}
                        onKeyDown={(e) => {
                            if (e.key === "Enter") {
                                loadStudent();
                            }
                        }}
                        className="grow"
                        placeholder="Student ID" />
                </label>


                <span className={`badge ${student.student_id > 0 ? 'badge-success' : 'badge-warning'}`}>{student.student_name}</span>
            </nav>

            <div className='flex flex-wrap'>
                <div className='w-full lg:w-2/3 p-4 space-y-2'>
                    <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6'>
                        {
                            data.map((item, index) => (
                                <PosItem key={index} item={item} onClick={addItem} />
                            ))
                        }
                    </div>
                </div>
                <div className='w-full lg:w-1/3 p-4'>
                    {
                        cart.length > 0 ? <div className='bg-base-300 rounded'>
                            <div className='divide-y divide-gray-400 dark:divide-gray-600 p-2'>
                                {/** List */}

                                {
                                    cart.map((item, index) => (
                                        <CartItem key={index}
                                            index={index}
                                            item={item}
                                            updateItemCount={updateItemCount}
                                            removeItem={removeItem} />
                                    ))
                                }
                            </div>

                            <div className='divider'></div>
                            <div className='flex justify-between p-4'>
                                <h2 className='text-gray-500'>Total:  {totalAmount}</h2>
                                <button className={'btn btn-success rounded'} onClick={createBill}>
                                    Create invoice
                                </button>
                            </div>
                        </div> : ''
                    }
                </div>
            </div>
        </>
    )
}

export default Pos