import React, { useRef } from 'react'
import Cookies from 'js-cookie'
import InventoryItem from './components/InventoryItem';
import { useAlertContext } from '../../context/AlertContext';
import { BuildingStorefrontIcon, CurrencyRupeeIcon, PlusCircleIcon, ShoppingBagIcon } from '@heroicons/react/24/solid';




const AdminInventory = () => {

    const { showLoading, showToast } = useAlertContext();
    const ref = useRef()
    const formRef = useRef()
    const [data, setData] = React.useState([]);
    const [form, setForm] = React.useState({ item_name: '', item_stock: 0, item_price: 0 });



    const fetchInventories = React.useCallback(async () => {

        const token = Cookies.get("authToken");
        if (!token) {
            console.error("Authentication token is missing");
            return;
        }

        showLoading(true)
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/inventory`, {
                method: 'GET',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
            });

            if (network.ok) {
                let responseData = await network.json();
                if (Array.isArray(responseData)) {
                    setData(responseData);
                } else {
                    console.error("Unexpected data format", responseData);
                    setData([]);
                }
            } else {
                console.error(`Error: ${network.status} ${network.statusText}`);
            }
        } catch (error) {
            console.error("Error fetching inventory:", error);
        } finally {
            showLoading(false);
        }
    }, []); // eslint-disable-next-line react-hooks/exhaustive-deps

    React.useEffect(() => {
        fetchInventories();
    }, [fetchInventories]);


    const updateItem = async (index, item, price, stock) => {
        const stockX = stock.length === 0 ? 0 : stock
        setData((prev) => {
            // Otherwise, update the count
            return prev.map((item, i) =>
                i === index ? { ...item, item_price: price, item_stock: stockX } : item
            );
        });

        const newData = { ...item, item_price: price, item_stock: stockX }

        const token = Cookies.get("authToken");
        try {
            await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/inventory`, {
                method: 'PUT',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(newData)
            })
        } catch (error) {
            showLoading(false)
        }
    }

    const handleChange = (e) => {
        const { name, value } = e.target;
        setForm({ ...form, [name]: value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        console.log('Form submit')
        const token = Cookies.get("authToken");
        try {
            const call = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/inventory`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(form)
            })

            if (call.ok) {
                showToast("Item added successfully", "success")
                formRef.current.submit()
            } else {
                showToast("Failed", "warning")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            fetchInventories()
        }
    }


    return (
        <>
            <div className='flex justify-between items-center px-4 mt-4'>
                <label className='font-bold text-2xl flex items-center gap-2'>
                    <BuildingStorefrontIcon className='size-6' /> Inventory items
                </label>
                <button className='btn btn-secondary rounded' onClick={() => ref.current.showModal()}>
                    <PlusCircleIcon className='size-6' />
                    Add new item
                </button>
            </div>

            <div className="divider"></div>


            <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6 p-6'>
                {
                    data.map((item, index) => (
                        <InventoryItem key={index} index={index} item={item} onClick={updateItem} />
                    ))
                }
            </div>

            <dialog ref={ref} className="modal">
                <div className="modal-box">
                    <h3 className="font-bold text-lg">
                        Add new item
                    </h3>

                    <div className='py-4'>
                        <form method="dialog" ref={formRef} className='space-y-2' onSubmit={handleSubmit}>
                            <label className="input input-bordered rounded focus:outline-none flex items-center gap-2">
                                <BuildingStorefrontIcon className='size-6' />
                                <input type="text"
                                    required
                                    name='item_name'
                                    value={form.item_name}
                                    onChange={handleChange}
                                    className="grow"
                                    placeholder="Item name" />
                            </label>

                            <label className="input input-bordered rounded focus:outline-none flex items-center gap-2">
                                <CurrencyRupeeIcon className='size-6' />
                                <input type="text"
                                    required
                                    name='item_price'
                                    value={form.item_price}
                                    onChange={handleChange}
                                    className="grow"
                                    placeholder="Price" />
                            </label>

                            <label className="input input-bordered rounded focus:outline-none flex items-center gap-2">
                                <ShoppingBagIcon className='size-6' />
                                <input type="text"
                                    required
                                    name='item_stock'
                                    value={form.item_stock}
                                    onChange={handleChange}
                                    className="grow"
                                    placeholder="Quantity" />
                            </label>
                        </form>
                    </div>

                    <div className="modal-action">
                        <button onClick={() => formRef.current.requestSubmit()} className="btn btn-primary rounded gap-4">
                            <PlusCircleIcon className="size-6" /> Add
                        </button>
                    </div>
                </div>
            </dialog>
        </>

    )
}

export default AdminInventory