import React from 'react';
import Cookies from 'js-cookie';
import { useAlertContext } from '../../context/AlertContext';

const AdminSchool = () => {
    document.title = 'SCHOOL';
    const { showLoading } = useAlertContext()
    const [userInfo, setUserInfo] = React.useState({
        school_name: '',
        school_address: '',
        school_district: '',
        school_state: '',
        school_phone: '',
        school_logo: ''
    });

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setUserInfo((prevInfo) => ({
            ...prevInfo,
            [name]: value,
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        const token = Cookies.get('authToken');
        showLoading(true)
        try {
            await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/school`, {
                method: 'PUT',
                headers: {
                    Authorization: `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(userInfo)
            });
        } catch (error) {
            console.error(error);
        } finally {
            showLoading(false)
        }
    };

    React.useEffect(() => {
        (async () => {
            showLoading(true)
            const token = Cookies.get('authToken');
            try {
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/school`, {
                    method: 'GET',
                    headers: {
                        Authorization: `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                });

                if (network.ok) {
                    let data = await network.json();
                    setUserInfo(data);
                } else {
                    const errorText = await network.text();
                    console.error(errorText);
                }
            } catch (error) {
                console.error(error);
            } finally {
                showLoading(false)
            }
        })();
    }, []);

    return (
        <>
            <div className="max-w-5xl mx-auto m-10 bg-base-300 shadow-lg rounded p-8">
                <h2 className="text-2xl font-semibold mb-6 text-center">School Information</h2>

                {/* Grid layout with two columns */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                    {/* Left Column: Form */}
                    <form onSubmit={handleSubmit} className="space-y-4">
                        {/* School Name */}
                        <div>
                            <label htmlFor="school_name" className="block text-sm font-medium text-gray-700">
                                School Name
                            </label>
                            <input
                                type="text"
                                id="school_name"
                                name="school_name"
                                value={userInfo.school_name}
                                onChange={handleInputChange}
                                className="input input-border w-full rounded focus:outline-none"
                                placeholder="Enter your school name"
                            />
                        </div>

                        {/* School Address */}
                        <div>
                            <label htmlFor="school_address" className="block text-sm font-medium text-gray-700">
                                Address
                            </label>
                            <input
                                type="text"
                                id="school_address"
                                name="school_address"
                                value={userInfo.school_address}
                                onChange={handleInputChange}
                                className="input w-full input-border rounded focus:outline-none"
                                placeholder="Enter your address"
                            />
                        </div>

                        {/* District */}
                        <div>
                            <label htmlFor="school_district" className="block text-sm font-medium text-gray-700">
                                District
                            </label>
                            <input
                                type="text"
                                id="school_district"
                                name="school_district"
                                value={userInfo.school_district}
                                disabled
                                onChange={handleInputChange}
                                className="input input-border rounded w-full focus:outline-none"
                                placeholder="Enter your district"
                            />
                        </div>

                        {/* State */}
                        <div>
                            <label htmlFor="school_state" className="block text-sm font-medium text-gray-700">
                                State
                            </label>
                            <input
                                type="text"
                                id="school_state"
                                name="school_state"
                                value={userInfo.school_state}
                                disabled
                                onChange={handleInputChange}
                                className="input input-border rounded w-full focus:outline-none"
                                placeholder="Enter your state"
                            />
                        </div>

                        {/* Phone */}
                        <div>
                            <label htmlFor="school_phone" className="block text-sm font-medium text-gray-700">
                                Phone
                            </label>
                            <input
                                type="tel"
                                id="school_phone"
                                name="school_phone"
                                value={userInfo.school_phone}
                                onChange={handleInputChange}
                                className="input input-border rounded w-full focus:outline-none"
                                placeholder="Enter your phone number"
                            />
                        </div>

                        {/* Submit Button */}
                        <div>
                            <button
                                type="submit"
                                className="w-full bg-indigo-600 text-white py-2 px-4 rounded-md hover:bg-indigo-700 focus:ring-indigo-500 focus:outline-none"
                            >
                                Submit
                            </button>
                        </div>
                    </form>

                    {/* Right Column: Image */}
                    <div className="flex justify-center items-center">
                        <img
                            src={userInfo.school_logo === null ? '/logo512.png' : userInfo.school_logo}
                            alt="School Placeholder"
                            className="w-full max-w-sm rounded-md shadow-md"
                        />
                    </div>
                </div>
            </div>
        </>
    );
};

export default AdminSchool;
