import React, { useRef } from "react";
import Cookies from "js-cookie";
import Session from "./components/Session";
import { CalendarDateRangeIcon, CalendarIcon, PlusIcon } from "@heroicons/react/24/solid";
import { useAlertContext } from "../../context/AlertContext";



const AdminSession = () => {
    const ref = useRef()
    const formRef = useRef()
    const { showLoading, showToast } = useAlertContext()
    const [sessions, setSessions] = React.useState([])

    React.useEffect(() => {
        ; (async () => {
            showLoading(true)
            const token = Cookies.get("authToken");
            try {
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/session/all`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                })
                if (network.ok) {
                    let data = await network.json()
                    setSessions(data)
                } else {
                    const errorText = await network.text();
                    showToast(errorText, "error")
                }
            } catch (error) {
                showToast(error.message, "error")
            } finally {
                showLoading(false)
            }
        })()
    }, [])


    const [formData, setFormData] = React.useState({
        start: "",
        end: "",
    });

    const handleSubmit = async (e) => {
        e.preventDefault();
        const token = Cookies.get("authToken");
        showLoading(true)
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/session`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ start: formData.start, end: formData.end })
            })

            if (network.ok) {
                let data = await network.json()
                setSessions((prevSessions) => [...prevSessions, data]);
                formRef.current.submit()
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }

    }
    const handleChange = (e) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value,
        });
    };

    const roll = async (session_id) => {
        showLoading(true)
        const token = Cookies.get("authToken");
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/service/rollnumber`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ session: session_id })
            })


            if (network.ok) {
                showToast("Please check after few minutes. We are generating roll number for all classes.", 'success')
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }
    }

    return (
        <>

            <div className='flex justify-between items-center px-4 mt-4'>
                <label className='font-bold text-2xl flex items-center gap-2'>
                    <CalendarDateRangeIcon className='size-6' /> Session
                </label>
                <button className='btn btn-secondary rounded' onClick={() => ref.current.showModal()}>
                    <PlusIcon className='size-6' />
                    Add new Session
                </button>
            </div>

            <div className="divider"></div>


            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 p-6">
                {
                    sessions.map((session, index) => (
                        <Session key={index} sessionId={session.session_id} start={session.start} end={session.end} admission={session.admission} status={session.status} roll={roll} />
                    ))
                }
            </div>


            <dialog ref={ref} className="modal">
                <div className="modal-box">
                    <h3 className="font-bold text-lg">
                        Add new Academic Session
                    </h3>

                    <div className='py-4'>
                        <form method="dialog" ref={formRef} className='space-y-2' onSubmit={handleSubmit}>
                            <label className="input input-bordered rounded focus:outline-none flex items-center gap-2">
                                <CalendarIcon className='size-6' />
                                <input type="text"
                                    required
                                    name='start'
                                    value={formData.start}
                                    onChange={handleChange}
                                    className="grow"
                                    placeholder="Session start year" />
                            </label>

                            <label className="input input-bordered rounded focus:outline-none flex items-center gap-2">
                                <CalendarIcon className='size-6' />
                                <input type="text"
                                    required
                                    name='end'
                                    value={formData.end}
                                    onChange={handleChange}
                                    className="grow"
                                    placeholder="Session end year" />
                            </label>
                        </form>
                    </div>

                    <div className="modal-action">
                        <button onClick={() => formRef.current.requestSubmit()} className="btn btn-primary rounded gap-4">
                            <PlusIcon className="size-6" /> Add
                        </button>
                    </div>
                </div>
            </dialog>
        </>
    );
};

export default AdminSession;
