import { MagnifyingGlassIcon, UserGroupIcon } from '@heroicons/react/24/solid'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import Cookies from 'js-cookie'
import { useAlertContext } from '../../context/AlertContext'


const FindStudent = () => {
    const { showLoading, showToast } = useAlertContext()
    const [raw, setRaw] = useState([])
    const [students, setStudents] = useState([])
    const [searchQuery, setSearchQuery] = useState("");



    useEffect(() => {
        // Filter students based on the search query
        const filteredStudents = raw.filter(
            (student) =>
                student.student_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                student.student_id.toString().includes(searchQuery)
        );
        setStudents(filteredStudents)
    }, [searchQuery, raw])

    useEffect(() => {
        ; (async () => {
            const token = Cookies.get("authToken");
            try {
                showLoading(true)
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/student`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                })
                if (network.ok) {
                    let data = await network.json()
                    setStudents(data)
                    setRaw(data)
                } else {
                    const errorText = await network.text();
                    showToast(errorText, "error")
                }
            } catch (error) {
                showToast(error.message, "error")
            } finally {
                showLoading(false)
            }
        })()
    }, [])



    return (
        <>
            <div className='p-4 flex justify-between items-center'>
                <label className='font-bold text-2xl flex items-center gap-4'>
                    <UserGroupIcon className='size-6' /> Students
                </label>
                <label className="input input-bordered flex items-center gap-2">
                    <input type="text" value={searchQuery} onChange={(e) => setSearchQuery(e.target.value)} className="grow" placeholder="Search" />
                    <MagnifyingGlassIcon className='size-4' />
                </label>
            </div>

            <div className='divider mt-0'></div>


            <div className='p-4'>
                <table className="w-full table border border-base-300">
                    <thead>
                        <tr>
                            <th className="py-2 px-4 text-left font-semibold ">Name</th>
                            <th className="py-2 px-4 text-left font-semibold ">Student ID</th>
                            <th className="py-2 px-4 text-left font-semibold ">Phone</th>
                            <th className="py-2 px-4 text-left font-semibold ">Parent</th>
                            <th className="py-2 px-4 text-left font-semibold ">Gender</th>
                            <th className="py-2 px-4 text-left font-semibold ">Address</th>
                        </tr>
                    </thead>
                    <tbody className='divide-y divide-base-300'>
                        {students.map((student, index) => (
                            <tr key={index}>
                                <td className="py-2 px-4">
                                    <div className="flex items-center space-x-3">
                                        <img
                                            src={student.photo === null ? "/favicon.ico" : student.photo}
                                            alt={`${student.student_name}`}
                                            className="w-8 h-8 rounded-full"
                                        />
                                        <span><Link className='hover:underline' to={`${student.student_id}`}>{`${student.student_name}`}</Link> </span>
                                    </div>
                                </td>
                                <td className="py-2 px-4">{student.student_id}</td>
                                <td className="py-2 px-4">{student.phone}</td>
                                <td className="py-2 px-4">{student.parent_name}</td>
                                <td className="py-2 px-4 uppercase">{student.gender}</td>
                                <td className="py-2 px-4">{student.address}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </>
    )
}

export default FindStudent