import React, { useState } from 'react'
import Cookies from 'js-cookie'
import { useNavigate, useParams } from 'react-router-dom';

import { useReactToPrint } from 'react-to-print';
import { CreditCardIcon, PrinterIcon, ReceiptRefundIcon } from '@heroicons/react/24/solid';



const modeList = [
    { id: 'cash', name: 'Cash' },
    { id: 'card', name: 'Card' },
    { id: 'upi', name: 'UPI' },
    { id: 'online', name: 'Online' }
]



const Bill = () => {

    const [discount, setDiscount] = useState(0)

    const [mode, setOrder] = React.useState(modeList[0].id)
    const navigate = useNavigate()
    const billRef = React.useRef();
    const { id } = useParams()
    const [alert, setAlert] = React.useState({ open: false, title: '', body: 'text' })
    const [bill, setBill] = React.useState({})
    const [school, setSchool] = React.useState({})
    const [items, setItems] = React.useState([])
    const [student, setStudent] = React.useState({})



    React.useEffect(() => {
        ; (async () => {
            const token = Cookies.get("authToken");
            try {
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/bill/${id}`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                })

                if (network.ok) {
                    let data = await network.json()
                    setSchool({ name: data.school_name, address: data.school_address, phone: data.school_phone, photo: data.school_logo, district: data.school_district, state: data.school_state })
                    setStudent({ name: data.student_name, id: data.student_id })
                    setBill({ date: data.bill_date.split("T")[0], id: data.bill_id, status: data.bill_status, amount: data.bill_amount, discount: data.discount_amount })
                    setItems(data.items)
                } else {
                    const errorText = await network.text();
                    setAlert({ open: true, title: 'Network', body: errorText })
                }
            } catch (error) {
                setAlert({ open: true, title: 'Network', body: "Network error !!" })
            }
        })()
    }, [id])


    const close = () => {
        setAlert({ ...alert, open: false })
        navigate('/billing')
    }
    document.title = `INVOICE #${id}`
    const totalAmount = items.reduce((sum, item) => sum + parseFloat(item.amount), 0);

    const reactToPrintFn = useReactToPrint({
        contentRef: billRef,
        documentTitle: "Invoice"
    });

    const confirmPaymant = async () => {

        const token = Cookies.get("authToken");
        try {
            let call = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/payment`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    bill_id: id,
                    mode: mode,
                    amount: bill.amount,
                    type: 'credit',
                    description: `#${id} : Bill credit`
                })
            })

            if (call.ok) {
                setBill({ ...bill, status: 'Paid' })
            }
        } catch (error) {
        }
    }

    const refundPaymant = async () => {

        const token = Cookies.get("authToken");
        try {
            let call = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/payment`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    bill_id: id,
                    mode: mode.id,
                    amount: `-${bill.amount}`,
                    type: 'refund',
                    description: `#${id} : Bill refund`
                })
            })

            if (call.ok) {
                setBill({ ...bill, status: 'Refund' })
            }
        } catch (error) {

        }
    }

    return (
        <div>

            <div className='flex flex-wrap'>
                <div className="w-full lg:w-1/4 p-4">

                    <div className='w-full bg-base-300 rounded rounded-b-none'>
                        <h2 className='py-2 px-4 text-left font-semibold text-base-content uppercase'>Payment Mode</h2>
                    </div>

                    <div className="w-full">

                        <select value={mode} onChange={(e) => setOrder(e.target.value)} className="select select-bordered rounded-none focus:outline-none w-full">
                            {modeList.map((person) => (
                                <option
                                    key={person.name}
                                    value={person.id}>
                                    {person.name}
                                </option>
                            ))}
                        </select>

                        <label className="input input-bordered rounded-none flex items-center gap-2">
                            Discount
                            <input type="text" className="grow" value={discount} onChange={(e) => setDiscount(Number(e.target.value))} placeholder="amount" />
                        </label>

                    </div>

                    <button
                        className={'btn btn-success w-full rounded rounded-t-none'}
                        onClick={confirmPaymant}
                        disabled={bill.status === "Unpaid" ? false : true}>
                        <CreditCardIcon className='size-6' />
                        Confirm payment
                    </button>

                    {/* Print Button */}
                    <div className='w-full bg-base-300 rounded rounded-b-none mt-4'>
                        <h2 className='py-2 px-4 text-left font-semibold uppercase'>Actions</h2>
                    </div>

                    <div className='flex flex-wrap'>
                        <div className='w-1/2'>
                            <button
                                className={'btn btn-warning rounded rounded-e-none w-full'}
                                onClick={refundPaymant}
                                disabled={bill.status === "Paid" ? false : true} >
                                <ReceiptRefundIcon className='size-6' /> Refund
                            </button>
                        </div>
                        <div className='w-1/2'>
                            <button
                                className={'btn btn-accent rounded-s-none rounded w-full'}
                                onClick={reactToPrintFn}
                                disabled={bill.status === "Unpaid" ? true : false} >
                                <PrinterIcon className='size-6' /> Print
                            </button>
                        </div>
                    </div>
                </div>
                <div className="w-full lg:w-3/4 p-4">
                    <div>

                        <div className='flex justify-end'>
                            <span style={{ width: '794px' }} className='py-2 px-4 text-center font-semibold bg-indigo-600 dark:bg-slate-900  text-white dark:text-gray-300 uppercase'>Preview</span>
                        </div>

                        <div className='flex justify-end'>
                            <div ref={billRef} className="relative bg-white" style={{ width: '794px', height: '1123px' }}>
                                <div className="p-4">

                                    {/* Header Section */}
                                    <div className="border-b-2 pb-4 border-indigo-600 flex justify-between">
                                        <div className="text-start ">
                                            <h1 className="text-3xl font-extrabold text-gray-900 uppercase">{school.name}</h1>
                                            <p className="text-sm text-gray-600 mt-2">{school.address}</p>
                                            <p className="text-sm text-gray-600">{school.district} &middot; {school.state}</p>
                                            <p className="text-sm text-gray-600">Phone: {school.phone}</p>
                                        </div>
                                        <img src={school.photo === null ? '/logo512.png' : school.photo} alt="school logo" width={128} height={128} />
                                    </div>


                                    {/* Payment Status Section */}
                                    <div className="flex justify-between items-center">
                                        <div>
                                            <h2 className="text-2xl font-bold text-indigo-700">Bill To:</h2>
                                            <div className="text-md text-gray-700 mt-4">
                                                <p className="font-semibold"><span className="font-normal">{student.name}</span></p>
                                                <p className="font-semibold">Student ID: <span className="font-normal">{student.id}</span></p>
                                            </div>
                                        </div>
                                        <div className="text-md text-right pt-4 text-gray-900">
                                            <p className="font-semibold">Date: <span className="font-normal">{bill.date}</span></p>
                                            <p className="font-semibold">Bill No: <span className="font-normal">#{bill.id}</span></p>

                                            {/* Payment Status Graphics */}
                                            <div className={`mt-4 py-2 px-4 text-center font-bold ${bill.status === 'Paid' ? 'bg-green-200 text-green-700' : bill.status === 'Refund' ? 'bg-yellow-200 text-yellow-700' : 'bg-red-200 text-red-700'
                                                }`}>
                                                {bill.status}
                                            </div>
                                        </div>
                                    </div>

                                    {/* Combined Breakdown */}
                                    <div className="mt-8">
                                        <h2 className="text-2xl font-bold text-indigo-700 mb-4">Bill Details</h2>
                                        <table className="w-full text-left table-auto border-collapse">
                                            <thead>
                                                <tr>
                                                    <th className="border-b-2 border-gray-300 py-3 text-sm font-semibold text-gray-600">Description</th>
                                                    <th className="border-b-2 border-gray-300 py-3 text-sm font-semibold text-gray-600 text-right">Quantity</th>
                                                    <th className="border-b-2 border-gray-300 py-3 text-sm font-semibold text-gray-600 text-right">Amount</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {items.map((item, index) => (
                                                    <tr key={index}>
                                                        <td className="border-b py-3 text-gray-700">{item.description}</td>
                                                        <td className="border-b py-3 text-gray-700 text-right">
                                                            {item.quantity ? item.quantity : "-"}
                                                        </td>
                                                        <td className="border-b py-3 text-gray-700 text-right">{item.amount}</td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    </div>

                                    {/* Total Section */}
                                    <div className="mt-10 border-t-2 border-gray-300 pt-6">
                                        <p className="text-right text-2xl font-extrabold text-gray-900">Total:  {totalAmount}</p>
                                    </div>

                                    {/* Footer Branding Section */}
                                    <div className="absolute bottom-0 left-0 right-0 px-4 py-2">
                                        <div className="flex justify-between items-center">
                                            <p className="text-sm text-gray-500">Thank you !</p>
                                            <p className="text-sm text-gray-700 font-semibold">Powered by Messant</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Bill