import { TableCellsIcon } from '@heroicons/react/24/solid'
import React, { useCallback, useState } from 'react'
import { useAlertContext } from '../../context/AlertContext'
import Cookies from 'js-cookie'


const Marks = () => {
    const token = Cookies.get("authToken");

    const { showLoading, showToast } = useAlertContext()

    const [exams, setExams] = useState([])
    const [sessions, setSessions] = useState([])
    const [classes, setClasses] = useState([])
    const [subjects, setSubjects] = useState([])
    const [students, setStudents] = useState([])

    const [examId, setExamId] = useState(0)
    const [sessionId, setSessionId] = useState(0)
    const [classId, setClassId] = useState(0)
    const [subjectId, setSubjectId] = useState(0)



    const [inputData, setInputData] = React.useState(students.map(() => ({ fm: 0, mo: 0, uid: 0 })));

    React.useEffect(() => {
        setInputData(students.map((student) => ({ fm: 0, mo: 0, uid: student.student_id })));
    }, [students]);


    const handleInputChange = (index, field, value) => {
        setInputData(prev => {
            const updated = [...prev];
            updated[index][field] = value;
            return updated;
        });
    };


    const submitMarks = async () => {


        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/exam/marks`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    examId: examId,
                    sessionId: sessionId,
                    classId: classId,
                    subjectId: subjectId,
                    marks: inputData
                })
            })
            if (network.ok) {
                showToast("Marks submitted !", "success")
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }
    }


    const fetchExams = useCallback(async () => {
        showLoading(true)

        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/exam/active`, {
                method: 'GET',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
            })
            if (network.ok) {
                let data = await network.json()
                setExams(data)
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }
    }, [showLoading, showToast, token])

    const fetchSessions = useCallback(async () => {
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/session/active`, {
                method: 'GET',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
            })

            if (network.ok) {
                let data = await network.json()
                setSessions(data)
            }
        } catch (error) {

        }
    }, [token])

    const fetchClasses = useCallback(async () => {
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/class`, {
                method: 'GET',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
            })

            if (network.ok) {
                let data = await network.json()
                setClasses(data)
            }
        } catch (error) {

        }
    }, [token])

    React.useEffect(() => {
        fetchExams()
        fetchSessions()
        fetchClasses()
    }, [])


    React.useEffect(() => {
        ; (async () => {
            if (classId !== 0) {
                const token = Cookies.get("authToken");

                try {
                    let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/class/${classId}`, {
                        method: 'GET',
                        headers: {
                            'Authorization': `Bearer ${token}`,
                            'Content-Type': 'application/json',
                        },
                    })


                    if (network.ok) {
                        let data = await network.json()
                        setSubjects(data.subjects)
                    } else {
                        const errorText = await network.text();
                        showToast(errorText, "error")
                    }
                } catch (error) {
                    showToast(error.message, "error")
                }
            }
        })()
    }, [classId])



    React.useEffect(() => {
        ; (async () => {
            if (classId !== 0 && sessionId !== 0) {
                const token = Cookies.get("authToken");
                try {
                    showLoading(true)
                    let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/enrollment/${sessionId}/${classId}`, {
                        method: 'GET',
                        headers: {
                            'Authorization': `Bearer ${token}`,
                            'Content-Type': 'application/json',
                        }
                    })

                    if (network.ok) {
                        let data = await network.json()
                        setStudents(data)
                        showToast("Student data fetched...", "success")
                    } else {
                        const errorText = await network.text();
                        showToast(errorText, "error")
                    }
                } catch (error) {
                    showToast(error.message, "error")
                } finally {
                    showLoading(false)
                }
            }
        })()
    }, [classId, sessionId])








    return (
        <>
            <div className='flex justify-between items-center px-4 mt-4'>
                <label className='font-bold text-2xl flex items-center gap-2'>
                    <TableCellsIcon className='size-6' /> Marks Entry
                </label>
            </div>

            <div className="divider"></div>


            <div className="p-4 m-4 bg-base-300 rounded shadow-md flex flex-wrap">
                <div className="w-full flex flex-col lg:w-1/4 p-4">
                    <label htmlFor="session" className="mb-2">Exam</label>
                    <select onChange={(e) => setExamId(Number(e.target.value))} className='select select-primary rounded-none focus:outline-none w-full'>
                        <option disabled selected>Select exam</option>
                        {
                            exams.map((exam, index) => (
                                <option value={exam.exam_id} key={index}>{exam.exam_name}</option>
                            ))
                        }
                    </select>
                </div>

                <div className="w-full flex flex-col lg:w-1/4 p-4">
                    <label htmlFor="session" className="mb-2">Session</label>
                    <select onChange={(e) => setSessionId(Number(e.target.value))} className='select select-primary rounded-none focus:outline-none w-full'>
                        <option disabled selected>Select session</option>
                        {
                            sessions.map((session) => (
                                <option value={session.session_id} key={session.session_id}>{session.start} - {session.end}</option>
                            ))
                        }
                    </select>
                </div>

                <div className="w-full flex flex-col lg:w-1/4 p-4">
                    <label htmlFor="class" className="mb-2">Class</label>
                    <select onChange={(e) => setClassId(Number(e.target.value))} className="select select-primary w-full rounded-none">
                        <option value={0} disabled selected>Select class</option>
                        {
                            classes.map((cls) => (
                                <option value={cls.class_id} key={cls.class_id}>{cls.class_name}</option>
                            ))
                        }
                    </select>
                </div>

                <div className="w-full flex flex-col lg:w-1/4 p-4">
                    <label htmlFor="class" className="mb-2">Subject</label>
                    <select onChange={(e) => setSubjectId(Number(e.target.value))} className="select select-primary w-full rounded-none">
                        <option value={0} disabled selected>Select subject</option>
                        {
                            subjects.map((subject, index) => (
                                <option value={subject.subject_id} key={index}>{subject.subject_name}</option>
                            ))
                        }
                    </select>
                </div>


                <table className="w-full table border border-base-300">
                    <thead>
                        <tr>
                            <th className="py-2 px-4 text-left font-semibold ">Name</th>
                            <th className="py-2 px-4 text-left font-semibold ">Student ID</th>
                            <th className="py-2 px-4 text-left font-semibold ">Class roll</th>
                            <th className="py-2 px-4 text-left font-semibold ">Full marks</th>
                            <th className="py-2 px-4 text-left font-semibold ">Marks obtain</th>
                        </tr>
                    </thead>
                    <tbody className='divide-y divide-base-300'>
                        {students.map((student, index) => (
                            <tr key={index}>
                                <td className="py-2 px-4">
                                    <div className="flex items-center space-x-3">
                                        <img
                                            src={student.photo === null ? "/favicon.ico" : student.photo}
                                            alt={`${student.student_name}`}
                                            className="w-8 h-8 rounded-full"
                                        />
                                        <span>{student.student_name}</span>
                                    </div>
                                </td>
                                <td className="py-2 px-4">{student.student_id}</td>
                                <td className="py-2 px-4">{student.roll_number === null ? 'Not assigned' : student.roll_number}</td>
                                <td className="py-2 px-4">
                                    <input
                                        className="input input-bordered rounded-none focus:outline-none"
                                        value={inputData[index]?.fm || ""}
                                        onChange={(e) => handleInputChange(index, "fm", e.target.value)}
                                    />
                                </td>
                                <td className="py-2 px-4">
                                    <input
                                        className="input input-bordered rounded-none focus:outline-none"
                                        value={inputData[index]?.mo || ""}
                                        onChange={(e) => handleInputChange(index, "mo", e.target.value)}
                                    />
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>

                <div className='w-full flex justify-end' onClick={submitMarks}>
                    <button className='btn btn-success rounded'>
                        Save
                    </button>
                </div>

            </div>


        </>
    )
}

export default Marks