import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";

const Home = () => {
    const navigate = useNavigate();

    useEffect(() => {
        // Fetch user type from local storage
        const data = JSON.parse(localStorage.getItem("_staffData"));
        const userType = data.role
        // Redirect based on user type
        if (userType === "admin") {
            navigate("/admin");
        } else if (userType === "teacher") {
            navigate("/teacher");
        } else if (userType === "staff") {
            navigate("/staff");
        } else {
            // Optional: Redirect to login or error page if user type is undefined
            navigate("/login");
        }
    }, [navigate]);

    // While redirecting, you can show a loader or empty screen
    return <div>Redirecting...</div>;
};

export default Home;
