import React from 'react';
import Barcode from 'react-barcode';
import html2canvas from 'html2canvas';

const IDCard = ({ id, name, photo, phone, school, type }) => {

    const cardRef = React.useRef(null);

    const downloadImage = async (format = 'png') => {
        const cardElement = cardRef.current;
        if (cardElement) {
            try {
                const canvas = await html2canvas(cardElement, {
                    useCORS: true,
                    backgroundColor: '#ffffff',  // Fallback color for unsupported formats
                    scale: 2,  // Lower scale for better performance
                    logging: true,  // Enable logging for troubleshooting
                });

                const dataUrl = format === 'jpeg' ? canvas.toDataURL('image/jpeg') : canvas.toDataURL('image/png');
                const link = document.createElement('a');
                link.download = `${id}_${name}_ID_card.${format}`;
                link.href = dataUrl;

                // Trigger the download
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
            } catch (error) {
                console.error('Error capturing the card image:', error);
            }
        } else {
            console.error('cardRef is not pointing to a valid DOM element');
        }
    };

    return (
        <>
            <div className='flex justify-center items-center'>
                <div ref={cardRef} className="w-[2.125in] h-[3.375in] relative bg-white">

                    {/* Top Background with Gradient */}
                    <div className="absolute top-0 w-full h-24 bg-blue-600"></div>
                    {/* School Name */}
                    <div className="text-center absolute top-0 left-0 right-0 h-12 px-2 pt-1">
                        <h1 className="text-base font-semibold text-white tracking-wider uppercase">{school}</h1>
                    </div>

                    {/* Circle Image Placeholder */}
                    <div className="flex justify-center relative mt-14">
                        <div className="w-20 h-20 rounded-full border-4 border-white bg-gray-200">
                            <img
                                src={photo === null ? '/default.jpg' : photo}
                                alt="Profile"
                                className="w-full h-full object-cover rounded-full"
                            />
                        </div>
                    </div>

                    {/* User Info */}
                    <div className="text-center relative z-10">
                        <h2 className="text-lg font-bold text-gray-900 uppercase">{name}</h2>
                        <p className="text-sm text-gray-500">{type}</p>
                    </div>

                    {/* Barcode && Contact Info */}
                    <div className="bottom-0 left-0 right-0 absolute z-10">
                        <div className="text-xs text-gray-700 relative z-10 p-2">
                            <p className="flex justify-between">
                                <span className="font-semibold">ID NO</span>
                                <span>{id}</span>
                            </p>
                            <p className="flex justify-between">
                                <span className="font-semibold">PHONE</span>
                                <span>{phone}</span>
                            </p>
                        </div>
                        <Barcode
                            className={'w-full'}
                            value={id}
                            format="CODE128"
                            displayValue={false}
                            width={2}
                            height={36}
                            background="#ffffff"
                            lineColor="#000000"
                        />
                    </div>

                </div>
            </div>
            <div className='flex justify-center'>
                <button onClick={downloadImage} className="btn btn-primary !w-[2.125in] rounded-t-none">
                    Download ID Card
                </button>
            </div>
        </>
    );
};

export default IDCard;
