import { CurrencyRupeeIcon, ShoppingBagIcon } from '@heroicons/react/24/solid'
import React from 'react'

const PosItem = ({ item, onClick }) => {
    return (
        <div className='rounded p-4 bg-base-300 text-base-content space-y-3'>
            <p>{item.item_name}</p>
            <div className='flex justify-between'>
                <span className={`badge badge-info`}>
                    <CurrencyRupeeIcon className='size-4' /> {item.item_price}
                </span>
                <span className={`badge ${item.item_stock > 10 ? 'badge-success' : item.item_stock === 0 ? 'badge-error' : 'badge-warning'}`}>
                    <ShoppingBagIcon className='size-4' /> {item.item_stock}
                </span>
            </div>
            <button
                className={`btn btn-sm w-full rounded ${item.item_stock > 0 ? 'btn-primary' : 'btn-error'}`}
                disabled={item.item_stock === 0 ? true : false}
                onClick={() => onClick(item)}>
                {item.item_stock > 0 ? 'Add' : 'Out of stock'}
            </button>
        </div>
    )
}

export default PosItem