import { ChevronRightIcon, ExclamationCircleIcon, InformationCircleIcon, PlusCircleIcon, PlusIcon } from '@heroicons/react/24/solid'
import React from 'react'
import Cookies from 'js-cookie'
import Subject from './components/Subject'
import { Link } from 'react-router-dom'
import { useAlertContext } from '../../context/AlertContext'

const AdminClass = () => {

    const { showLoading, showToast } = useAlertContext()

    const [selectedClass, setSelectedClass] = React.useState(0)
    const [classes, setClasses] = React.useState([])
    const [classForm, setClassForm] = React.useState({ name: '' })
    const [subject, setSubject] = React.useState('')
    const [selectedSession, setSelectedSession] = React.useState(0)
    const [sessions, setSessions] = React.useState([])
    const [students, setStudents] = React.useState([])
    const [subjects, setSubjects] = React.useState([])
    const [fee, setFee] = React.useState({ "fee_id": 0, "class_id": 0, "admission": "0.00", "tuition": "0.00", "cycle": "Monthly" })

    React.useEffect(() => {
        ; (async () => {
            const token = Cookies.get("authToken");
            try {
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/class`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                })


                if (network.ok) {
                    let data = await network.json()
                    setClasses(data)

                } else {
                    const errorText = await network.text();
                    showToast(errorText, "error")
                }
            } catch (error) {
                showToast(error.message, "error")
            } finally {

            }


            try {
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/session/all`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                })
                if (network.ok) {
                    let data = await network.json()
                    setSessions(data)
                } else {
                    const errorText = await network.text();
                    showToast(errorText, "error")
                }
            } catch (error) {
                showToast(error.message, "error")
            } finally {

            }
        })()
    }, [])


    React.useEffect(() => {
        ; (async () => {
            if (selectedClass !== 0) {
                const token = Cookies.get("authToken");

                try {
                    let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/class/${selectedClass}`, {
                        method: 'GET',
                        headers: {
                            'Authorization': `Bearer ${token}`,
                            'Content-Type': 'application/json',
                        },
                    })


                    if (network.ok) {
                        let data = await network.json()
                        setSubjects(data.subjects)
                        setFee(data.fee)
                    } else {
                        const errorText = await network.text();
                        showToast(errorText, "error")
                    }
                } catch (error) {
                    showToast(error.message, "error")
                }
            }
        })()
    }, [selectedClass])


    const handleFeeChange = (e) => {
        setFee({
            ...fee,
            [e.target.name]: e.target.value,
        });
    };

    const updateFee = async () => {

        const token = Cookies.get("authToken");
        try {
            showLoading(true)
            const network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/fee`, {
                method: 'PUT',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(fee)
            })


            if (network.ok) {
                showToast("Fees updated", "success")
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }
    }


    const addSubject = async () => {
        if (subject.length === 0) {
            return
        }

        const token = Cookies.get("authToken");
        try {
            showLoading(true)
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/subject/${selectedClass}`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ name: subject })
            })


            if (network.ok) {
                let data = await network.json()
                setSubject('')
                setSubjects((prev) => [...prev, data]);
                showToast("Subject added", "success")
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }
    }

    const deleteSubject = async (subjectId) => {

        const updatedSubjects = subjects.filter(subject => subject.subject_id !== subjectId);
        setSubjects(updatedSubjects);

        const token = Cookies.get("authToken");
        try {
            await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/subject/${subjectId}`, {
                method: 'DELETE',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                }
            })
        } catch (error) {
            console.error(error)
        }
    }

    const loadStudents = React.useCallback(async () => {
        if (selectedSession !== 0 && selectedClass.classId !== '') {
            const token = Cookies.get("authToken");
            try {
                showLoading(true)
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/enrollment/${selectedSession}/${selectedClass}`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    }
                })

                if (network.ok) {
                    let data = await network.json()
                    setStudents(data)
                    showToast("Student data fetched...", "success")
                } else {
                    const errorText = await network.text();
                    showToast(errorText, "error")
                }
            } catch (error) {
                showToast(error.message, "error")
            } finally {
                showLoading(false)
            }
        }
    }, [selectedClass, selectedSession, showLoading, showToast])


    const handleChange = (e) => {
        setClassForm({
            ...classForm,
            [e.target.name]: e.target.value,
        });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (classForm.name.length === 0) {
            return
        }
        showLoading(true)
        setClassForm({ ...classForm, name: '' })
        const token = Cookies.get("authToken");
        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/class`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ name: classForm.name })
            })
            if (network.ok) {
                let data = await network.json()
                setClasses((prev) => [...prev, data]);
                showToast("Class added", "success")
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }
    }

    return (
        <>
            <div className='flex justify-between items-center px-4 mt-4'>
                <label className='font-bold text-2xl flex items-center gap-2'>
                    Class

                    <select value={selectedClass} onChange={(e) => setSelectedClass(Number(e.target.value))} className="select select-bordered w-full max-w-xs">
                        <option value={0}>Select class</option>
                        {
                            classes.map((cls) => (
                                <option value={cls.class_id} key={cls.class_id}>{cls.class_name}</option>
                            ))
                        }
                    </select>
                </label>


                <div className='flex'>
                    <label className="input input-bordered rounded rounded-e-none flex items-center gap-2 outline-none">
                        Class
                        <input
                            type="text"
                            name='name'
                            className="grow focus:outline-none"
                            value={classForm.name}
                            onChange={handleChange}
                            placeholder="I / II / III / IV" />
                    </label>
                    <button className='btn btn-secondary rounded rounded-s-none' onClick={handleSubmit}>
                        <PlusIcon className='size-6' />
                        Add Class
                    </button>
                </div>
            </div>

            <div className="divider"></div>


            {
                selectedClass !== 0 ? <>

                    <div className='flex flex-wrap'>
                        {/** Subjects and Fees */}
                        <div className='w-full lg:w-1/4 p-4'>
                            <div className='w-full bg-base-300 rounded rounded-b-none'>
                                <h2 className='py-2 px-4 text-left font-semibold uppercase'>Subjects</h2>
                            </div>

                            <div className='relative w-full'>
                                <input
                                    id='subject'
                                    type="text"
                                    autoComplete="off"
                                    required
                                    value={subject}
                                    onChange={(e) => setSubject(e.target.value)}
                                    className={
                                        "w-full bg-gray-100 dark:bg-white/5 py-2 px-3 text-sm/6 text-black dark:text-white focus:outline-none data-[focus]:outline-2 data-[focus]:-outline-offset-2 data-[focus]:outline-white/25"
                                    }
                                    placeholder="Type new subject..."
                                />
                                <button onClick={addSubject} className='absolute right-0 top-1/2 transform -translate-y-1/2 flex p-2 bg-blue-500 text-white hover:bg-blue-700 transition duration-200'>
                                    <PlusCircleIcon className='h-6 w-6 me-2' /> Add
                                </button>
                            </div>

                            <div className='divide-y divide-base-300'>
                                {
                                    subjects.map((subject, index) => (
                                        <Subject key={index} onRemove={deleteSubject} id={subject.subject_id} label={subject.subject_name} />
                                    ))
                                }
                            </div>


                            <div className='w-full mt-4 bg-base-300 rounded rounded-b-none'>
                                <h2 className='py-2 px-4 text-left font-semibold uppercase'>Fee Structure</h2>
                            </div>
                            <div className='bg-base-200 p-4 rounded-b'>
                                <label className='font-semibold uppercase'>Admission Fees</label>
                                <input
                                    name='admission'
                                    type="text"
                                    autoComplete="off"
                                    required
                                    value={fee.admission}
                                    onChange={handleFeeChange}
                                    className={'input input-bordered rounded-none w-full focus:outline-none'}
                                    placeholder="Admission fees"
                                />

                                <label className='font-semibold uppercase'>Tuition Fees</label>
                                <input
                                    name='tuition'
                                    type="text"
                                    autoComplete="off"
                                    required
                                    value={fee.tuition}
                                    onChange={handleFeeChange}
                                    className={'input input-bordered rounded-none w-full focus:outline-none'}
                                    placeholder="Tuition fees"
                                />
                                <button onClick={updateFee} className='btn btn-primary rounded-none w-full mt-4'>
                                    <InformationCircleIcon className='h-6 w-6 me-2' /> Update
                                </button>
                            </div>
                        </div>

                        {/** Enrolled Students */}
                        <div className='w-full lg:w-2/4 p-4'>
                            <div className='w-full bg-base-300 rounded rounded-b-none flex justify-start items-center'>
                                <h2 className='py-2 px-4 text-left font-semibold uppercase'>Enrolled Students</h2>
                                <select onChange={(e) => setSelectedSession(Number(e.target.value))} className='select select-primary rounded-none focus:outline-none'>
                                    <option disabled selected value={0}>Select session</option>
                                    {
                                        sessions.map((session) => (
                                            <option value={session.session_id} key={session.session_id}>{session.start} - {session.end}</option>
                                        ))
                                    }
                                </select>


                                <button onClick={loadStudents} className='btn btn-primary rounded-none'>
                                    View <ChevronRightIcon className='h-6 w-6 ms-2' />
                                </button>
                            </div>

                            <table className="w-full table border border-base-300">
                                <thead>
                                    <tr>
                                        <th className="py-2 px-4 text-left font-semibold ">Name</th>
                                        <th className="py-2 px-4 text-left font-semibold ">Student ID</th>
                                        <th className="py-2 px-4 text-left font-semibold ">Phone</th>
                                        <th className="py-2 px-4 text-left font-semibold ">Class Roll</th>
                                        <th className="py-2 px-4 text-left font-semibold ">Gender</th>
                                    </tr>
                                </thead>
                                <tbody className='divide-y divide-base-300'>
                                    {students.map((student, index) => (
                                        <tr key={index}>
                                            <td className="py-2 px-4">
                                                <div className="flex items-center space-x-3">
                                                    <img
                                                        src={student.photo === null ? "/favicon.ico" : student.photo}
                                                        alt={`${student.student_name}`}
                                                        className="w-8 h-8 rounded-full"
                                                    />
                                                    <span><Link className='hover:underline' to={`/admin/student/${student.student_id}`}>{`${student.student_name}`}</Link> </span>
                                                </div>
                                            </td>
                                            <td className="py-2 px-4">{student.student_id}</td>
                                            <td className="py-2 px-4">{student.phone}</td>
                                            <td className="py-2 px-4">{student.roll_number === null ? 'Not assigned' : student.roll_number}</td>
                                            <td className="py-2 px-4 uppercase">{student.gender}</td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </div>

                </> :
                    <div className="p-6 max-w-lg lg:max-w-xl m-6 mx-auto rounded space-y-4">
                        <div className="text-center p-6 rounded bg-base-300">
                            <ExclamationCircleIcon className="mx-auto h-16 w-16" />
                            <h3 className="mt-4 text-2xl font-semibold">Please select a class</h3>
                        </div>
                    </div>
            }
        </>
    )
}

export default AdminClass