
import React from 'react'
import Cookies from 'js-cookie';


const Session = ({ sessionId, start, end, admission, status, roll }) => {
    const token = Cookies.get("authToken");

    const [admissionSwitch, setAdmission] = React.useState(false)
    const [statusSwitch, setStatus] = React.useState(false)

    React.useEffect(() => {
        setAdmission((admission === 1) ? true : false)
        setStatus((status === 1) ? true : false)
    }, [admission, status])


    const changeAdmission = async () => {
        setAdmission(!admissionSwitch)

        await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/session/${sessionId}`, {
            method: 'PUT',
            headers: {
                'Authorization': `Bearer ${token}`,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ admission: !admissionSwitch, status: statusSwitch })
        })
    }

    const changeStatus = async () => {
        setStatus(!statusSwitch)

        await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/session/${sessionId}`, {
            method: 'PUT',
            headers: {
                'Authorization': `Bearer ${token}`,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ admission: admissionSwitch, status: !statusSwitch })
        })
    }



    return (
        <>
            <div className='bg-base-300 p-4 w-full space-y-4'>
                <div className='flex justify-between items-center'>
                    <span className='text-sm uppercase'>Session</span>
                    <h2 className="text-lg font-bold">{start} - {end}</h2>
                </div>
                <div className='flex justify-between items-center'>
                    <span className='text-sm uppercase'>Admission</span>
                    <input type="checkbox"
                        className="toggle toggle-success"
                        checked={admissionSwitch}
                        onChange={changeAdmission} />
                </div>
                <div className='flex justify-between items-center'>
                    <span className='text-sm uppercase'>Active</span>
                    <input type="checkbox"
                        className="toggle toggle-success"
                        checked={statusSwitch}
                        onChange={changeStatus} />
                </div>
                <div className='flex justify-between items-center'>
                    <label className='text-sm uppercase'>Roll</label>
                    <div>
                        <button disabled={admissionSwitch} className={'btn btn-primary rounded btn-sm'} onClick={() => roll(sessionId)}>
                            Generate
                        </button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Session
