import React from "react";
import { Link } from "react-router-dom";
import { useAuth } from "../../context/AuthContext";
import { useAlertContext } from "../../context/AlertContext";

const LoginPage = () => {
  const { login } = useAuth();
  const { showLoading, showToast } = useAlertContext()

  const [formData, setFormData] = React.useState({
    id: "",
    password: "",
  });

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };


  const handleSubmit = async (e) => {
    e.preventDefault();
    showLoading(true);

    try {
      const res = await login(formData.id, formData.password);

      if (!res.status) {
        showToast(res.message, "error")
      }
    } catch (error) {
      showToast(error.message, "error")
    } finally {
      showLoading(false)
    }
  };

  // document.title = "Login to your school";

  return (
    <div className="flex min-h-screen items-center justify-center">
      <div className="p-8 space-y-4 rounded-lg shadow-md bg-base-300">
        <h2 className="text-2xl font-bold text-center">
          Login to Your Account
        </h2>
        <form onSubmit={handleSubmit}>
          <div className="form-control">
            <label className="label">
              <span className="label-text">Staff ID</span>
            </label>
            <input
              type="text"
              name="id"
              onChange={handleChange}
              placeholder="Enter your staff ID"
              className="input rounded focus:outline-none w-full"
              required
            />
          </div>
          <div className="form-control">
            <label className="label">
              <span className="label-text">Password</span>
            </label>
            <input
              type="password"
              name="password"
              onChange={handleChange}
              placeholder="Enter your password"
              className="input rounded focus:outline-none w-full"
              required
            />
            <label className="label">
              <Link href="#" className="label-text-alt link link-hover">
                Forgot password?
              </Link>
            </label>
          </div>
          <div className="form-control mt-4">
            <button className="btn btn-primary rounded w-full">Login</button>
          </div>
        </form>
        <p className="text-sm text-center">
          Dont have an account?{" "}
          <Link href="#" className="text-blue-500 hover:underline">
            Sign up
          </Link>
        </p>
      </div>
    </div>
  );
};

export default LoginPage;
