
import { CurrencyRupeeIcon, ShoppingBagIcon } from '@heroicons/react/24/solid'
import React from 'react'

const InventoryItem = ({ item, index, onClick }) => {

    const [price, SetPrice] = React.useState(item.item_price)
    const [quantity, SetQuantity] = React.useState(item.item_stock)

    return (
        <div className='rounded p-4 bg-base-300 space-y-4'>
            {/* Header Section */}
            <div className='flex flex-wrap justify-between gap-4'>
                <h2 className='font-semibold text-xl'>{item.item_name}</h2>
                <span
                    className={`badge ${item.item_stock === 0
                        ? 'badge-error'
                        : item.item_stock < 10
                            ? 'badge-warning'
                            : 'badge-success'
                        }`}>
                    {item.item_stock === 0
                        ? 'Out of stock'
                        : 'Stock : ' + item.item_stock}
                </span>
            </div>

            {/* Form Section */}
            <div className='flex flex-wrap gap-4 items-center'>
                {/* Price Input */}
                <label className="input rounded flex items-center gap-2 w-full">
                    <CurrencyRupeeIcon className='size-6' />
                    <input
                        name="price"
                        type="text"
                        autoComplete="off"
                        required
                        value={price}
                        onChange={(e) => SetPrice(e.target.value)}
                        placeholder="Price"
                        className="w-full md:w-auto"
                    />
                </label>

                {/* Quantity Input */}
                <label className={`input rounded flex items-center gap-2 w-full ${item.item_stock === 0
                    ? 'input-error'
                    : ''
                    }`}>
                    <ShoppingBagIcon className='size-6' />
                    <input
                        name="quantity"
                        type="text"
                        autoComplete="off"
                        required
                        value={quantity}
                        onChange={(e) => SetQuantity(e.target.value)}
                        placeholder="Quantity"
                        className={`w-full md:w-auto`}
                    />
                </label>

                {/* Update Button */}
                <button
                    className='btn btn-primary btn-outline rounded w-full'
                    onClick={() => onClick(index, item, price, quantity)}>
                    Update
                </button>
            </div>
        </div>

    )
}

export default InventoryItem

