import React from 'react'
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../../context/AuthContext';


const Navbar = ({ icon, label }) => {
    const navigate = useNavigate()
    const dropdownRef = React.useRef(null);
    const { logout } = useAuth();
    const [dropdownOpen, setDropdownOpen] = React.useState(false);
    const [data, setData] = React.useState({ name: '', school: '', photo: '' })

    const toggleDropdown = () => {
        setDropdownOpen(prev => !prev);
    };

    const handleLogout = () => {
        logout();
        setDropdownOpen(false);
        navigate('/')
    };

    React.useEffect(() => {
        const a = localStorage.getItem("_staffData")
        const b = JSON.parse(a)
        setData({ name: b.name, photo: b.image, school: b.school })
        const handleClickOutside = (event) => {
            if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
                setDropdownOpen(false);
            }
        };

        document.addEventListener('mousedown', handleClickOutside);
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, [dropdownRef]);


    return (
        < nav className='h-16 px-4 flex items-center justify-between fixed top-0 md:start-64 start-16 right-0 z-10 bg-base-200'>
            <div className="flex justify-center items-center">
                <div className="relative me-4">
                    <img
                        src={data.photo === null ? '/favicon.ico' : data.photo} // Replace with your profile image URL
                        alt="Profile"
                        width={40}
                        height={40}
                        className="rounded-full cursor-pointer"
                        onClick={toggleDropdown}
                        onError={(e) => {
                            e.target.onerror = null;
                            e.target.src = '/favicon.ico';
                        }}
                    />
                    {dropdownOpen && (
                        <div ref={dropdownRef} className="absolute left-0 mt-3 w-48 text-gray-800 dark:text-gray-400 bg-white border border-gray-300 dark:border-gray-700 dark:bg-slate-900 rounded-md shadow-lg z-10">
                            <div className="py-2">
                                <Link to="/profile" className="block px-4 py-2 hover:bg-gray-200 dark:hover:bg-slate-600">Profile</Link>
                                <button onClick={handleLogout} className="block w-full text-left px-4 py-2 hover:bg-gray-200 dark:hover:bg-slate-600">
                                    Logout
                                </button>
                            </div>
                        </div>
                    )}
                </div>

                <section>
                    <h2 className='text-lg uppercase font-semibold'>{data.name}</h2>
                    <span className='text-xs uppercase'>{data.school}</span>
                </section>
            </div>

            <div className="ml-auto">
                <button onClick={handleLogout} className=" text-sm px-4 py-2 hover:bg-base-300">
                    Logout
                </button>
            </div>
        </nav>
    )
}

export default Navbar