import React, { createContext, useContext, useState, useEffect } from "react";
import Cookies from "js-cookie";

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [loading, setLoading] = useState(true);
  const apiEndpoint = process.env.REACT_APP_API_ENDPOINT || "http://localhost:5000";

  useEffect(() => {
    (async () => {
      const token = Cookies.get("authToken");
      if (token) {
        setIsAuthenticated(true);
      }
      await new Promise((resolve) => setTimeout(resolve, 500));
      setLoading(false);
    })();
  }, []);

  const apiCall = async (url, method, data) => {
    try {
      const response = await fetch(url, {
        method,
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify(data),
      });
      if (response.ok) {
        return { data: await response.json(), error: null };
      } else {
        return { data: null, error: await response.text() };
      }
    } catch (error) {
      console.error("API Call error:", error);
      return { data: null, error: "Network error!" };
    }
  };

  const login = async (id, password) => {
    const { data, error } = await apiCall(`${apiEndpoint}/v1/login/staff`, "POST", { id, password });
    if (error) {
      return { status: false, message: error };
    }
    Cookies.set("authToken", data.token, { expires: 1, secure: false, sameSite: "Strict" });
    localStorage.setItem("_staffData", JSON.stringify(data));
    setIsAuthenticated(true);
    return { status: true, message: "" };
  };

  const logout = () => {
    setIsAuthenticated(false);
    Cookies.remove("authToken");
    localStorage.removeItem("_staffData");
  };

  return (
    <AuthContext.Provider value={{ isAuthenticated, loading, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  return useContext(AuthContext);
};
