import React from 'react'
import Cookies from "js-cookie";
import { BuildingStorefrontIcon, UserCircleIcon, UserGroupIcon, WalletIcon } from '@heroicons/react/24/solid';



const AdminDashboard = () => {
    document.title = "Dashboard"
    const [data, setData] = React.useState({ students: 0, staffs: 0, balance: 0, stocks: 0 })


    React.useEffect(() => {
        ; (async () => {
            const token = Cookies.get("authToken");
            try {
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/dashboard`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                })

                if (network.ok) {
                    let data = await network.json()

                    const students = data[0][0].student_count
                    const staffs = data[1][0].staff_count
                    const balance = data[2][0].wallet_balance
                    const stocks = data[3][0].stock_count
                    setData({ students: students, staffs: staffs, balance: balance, stocks: stocks })

                } else {
                    const errorText = await network.text();
                    console.log(errorText)
                }
            } catch (error) {

            } finally {

            }
        })()
    }, [])


    return (
        <>

            <div className="flex flex-wrap w-full p-6">
                <div className={`w-full p-4 md:w-1/2 lg:w-1/3 xl:w-1/4`}>
                    <div className={`flex p-4 space-x-4 rounded-lg shadow-md items-center bg-green-600 text-white`}>
                        <UserCircleIcon className='h-16 w-16' />
                        <div>
                            <h3 className="text-2xl font-bold">{data.students}</h3>
                            <p className="text-base">Students</p>
                        </div>
                    </div>
                </div>

                <div className={`w-full p-4 md:w-1/2 lg:w-1/3 xl:w-1/4`}>
                    <div className={`flex p-4 space-x-4 rounded-lg shadow-md items-center bg-blue-600 text-white`}>
                        <UserGroupIcon className='h-16 w-16' />
                        <div>
                            <h3 className="text-2xl font-bold">{data.staffs}</h3>
                            <p className="text-base">Staffs</p>
                        </div>
                    </div>
                </div>

                <div className={`w-full p-4 md:w-1/2 lg:w-1/3 xl:w-1/4`}>
                    <div className={`flex p-4 space-x-4 rounded-lg shadow-md items-center bg-indigo-600 text-white`}>
                        <WalletIcon className='h-16 w-16' />
                        <div>
                            <h3 className="text-2xl font-bold"> {data.balance}</h3>
                            <p className="text-base">Balance</p>
                        </div>
                    </div>
                </div>

                <div className={`w-full p-4 md:w-1/2 lg:w-1/3 xl:w-1/4`}>
                    <div className={`flex p-4 space-x-4 rounded-lg shadow-md items-center bg-gray-600 text-white`}>
                        <BuildingStorefrontIcon className='h-16 w-16' />
                        <div>
                            <h3 className="text-2xl font-bold">{data.stocks}</h3>
                            <p className="text-base">Stocks</p>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default AdminDashboard