import React from 'react'
import Cookies from 'js-cookie';
import { useNavigate } from 'react-router-dom';
import { useAlertContext } from '../../context/AlertContext';



const AdminAdmission = () => {
    const navigate = useNavigate()
    const { showLoading, showToast } = useAlertContext()
    const [classes, setClasses] = React.useState([])
    const [options, setOptions] = React.useState([])
    const [formData, setFormData] = React.useState({
        student_name: '',
        parent: '',
        dob: '',
        phone: '',
        address: '',
        gender: 'male',
        class: '',
        session: ''
    });

    const genders = [{ value: 'male', label: 'Male' }, { value: 'female', label: 'Female' }]

    React.useEffect(() => {
        ; (async () => {


            const token = Cookies.get("authToken");
            try {
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/session/admission`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                })

                if (network.ok) {
                    let data = await network.json()
                    setOptions([])
                    data.map((s) => (
                        setOptions((prev) => [...prev, { value: s.session_id, label: `${s.start} - ${s.end}` }])
                    ))
                }
            } catch (error) {

            }

            try {
                let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/class`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json',
                    },
                })


                if (network.ok) {
                    let data = await network.json()
                    setClasses([])
                    data.map((s) => (
                        setClasses((prev) => [...prev, { value: s.class_id, label: s.class_name }])
                    ))

                }
            } catch (error) {

            }
        })()
    }, [])


    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        const token = Cookies.get("authToken");

        try {
            showLoading(true)
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/student/admission`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(formData)
            })

            if (network.ok) {
                let data = await network.json()
                navigate('/admin/bills/' + data.bill)
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }

    };



    return (
        <>

            <div className="m-6 p-6 bg-base-300 rounded">
                <h2 className="text-3xl font-semibold text-center mb-4">Admission Form</h2>
                <form onSubmit={handleSubmit} className="space-y-6">


                    <div className='flex flex-wrap'>
                        <div className='w-1/2 p-6 space-y-2'>
                            <div>
                                <label className="block  mb-2">Student Name</label>
                                <input
                                    type="text"
                                    name="student_name"
                                    value={formData.student_name}
                                    onChange={handleChange}
                                    className="w-full input rounded focus:outline-none"
                                    placeholder="First Name"
                                    required
                                />
                            </div>

                            <div>
                                <label className="block  mb-2">Parent's Name</label>
                                <input
                                    type="text"
                                    name="parent"
                                    value={formData.parent}
                                    onChange={handleChange}
                                    className="w-full input rounded focus:outline-none"
                                    placeholder="Parent's Name"
                                    required
                                />
                            </div>

                            <div>
                                <label className="block  mb-2">Date of Birth</label>
                                <input
                                    type="date"
                                    name="dob"
                                    value={formData.dob}
                                    onChange={handleChange}
                                    className="w-full input rounded focus:outline-none"
                                    required
                                />
                            </div>

                            <div>
                                <label className="block  mb-2">Gender</label>
                                <select
                                    name="gender"
                                    value={formData.gender}
                                    onChange={handleChange}
                                    className="w-full input rounded focus:outline-none"
                                    required
                                >
                                    {genders.map(g => (
                                        <option key={g.value} value={g.value}>
                                            {g.label}
                                        </option>
                                    ))}
                                </select>
                            </div>
                        </div>

                        <div className='w-1/2 p-6 space-y-2'>
                            <div>
                                <label className="block  mb-2">Phone Number</label>
                                <input
                                    type="tel"
                                    name="phone"
                                    value={formData.phone}
                                    onChange={handleChange}
                                    className="w-full input rounded focus:outline-none"
                                    placeholder="Phone Number"
                                    required
                                />
                            </div>

                            <div>
                                <label className="block  mb-2">Address</label>
                                <textarea
                                    name="address"
                                    value={formData.address}
                                    onChange={handleChange}
                                    className="w-full input rounded focus:outline-none"
                                    placeholder="Address"
                                    rows="3"
                                    required
                                ></textarea>
                            </div>

                            <div>
                                <label className="block  mb-2">Session</label>
                                <select
                                    name="session"
                                    value={formData.session}
                                    onChange={handleChange}
                                    className="w-full input rounded focus:outline-none"
                                    required
                                >
                                    <option value="" disabled>Select Session</option>
                                    {options.map(option => (
                                        <option key={option.value} value={option.value}>
                                            {option.label}
                                        </option>
                                    ))}
                                </select>
                            </div>

                            <div>
                                <label className="block  mb-2">Class</label>
                                <select
                                    name="class"
                                    value={formData.class}
                                    onChange={handleChange}
                                    className="w-full input rounded focus:outline-none"
                                    required
                                >
                                    <option value="" disabled>Select Class</option>
                                    {classes.map(c => (
                                        <option key={c.value} value={c.value}>
                                            {c.label}
                                        </option>
                                    ))}
                                </select>
                            </div>
                        </div>
                    </div>

                    <div className='flex justify-end'>
                        <button type="submit" className={`rounded btn btn-primary`}>
                            Submit
                        </button>
                    </div>
                </form>
            </div>
        </>
    )
}

export default AdminAdmission