import { MinusCircleIcon, PlusCircleIcon } from '@heroicons/react/24/solid'
import React from 'react'

const CartItem = ({ index, item, updateItemCount, removeItem }) => {


    const [total, setTotal] = React.useState(0)

    React.useEffect(() => {
        setTotal(item.count * item.price)
    }, [item.count, item])

    const addItem = () => {
        if (item.count < item.stock) { // Assuming stock is 10
            updateItemCount(index, item.count + 1);
        }
    };

    const subtractItem = () => {
        if (item.count > 1) {
            updateItemCount(index, item.count - 1);
        } else {
            removeItem(index); // If count goes below 1, remove the item
        }
    };

    return (
        <div className='p-2'>
            <p>{index + 1}. {item.name}</p>
            <div className='flex justify-between mt-4'>
                <div className='flex justify-start'>
                    <button
                        onClick={subtractItem}
                        className='w-8 h-8 rounded-full text-white'>
                        <MinusCircleIcon className='w-8 h-8' />
                    </button>
                    <input
                        type='text'
                        disabled
                        value={item.count}
                        className='w-8 outline-none shadow-none mx-2 border text-center'
                        placeholder='0' />
                    <button
                        onClick={addItem}
                        className='w-8 h-8 rounded-full text-white'>
                        <PlusCircleIcon className='w-8 h-8' />
                    </button>
                </div>
                <div>
                    <span className='text-gray-500 text-sm'>Amount:  {total}</span>
                </div>
            </div>
        </div>
    )
}

export default CartItem