import React, { useState } from "react";
import { useAlertContext } from "../../context/AlertContext";
import Cookies from "js-cookie";
import { useNavigate } from "react-router-dom";



const StaffForm = () => {

    const navigate = useNavigate()

    const { showLoading, showToast } = useAlertContext()

    const [formData, setFormData] = useState({
        staff_name: "",
        staff_phone: "",
        staff_email: "",
        staff_gender: "male",
        role: "teacher",
        hire_date: "",
        salary: "",
        bank_name: "",
        account_no: "",
        ifsc_code: "",
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        console.log(formData);
        showLoading(true)
        const token = Cookies.get("authToken");

        try {
            let network = await fetch(`${process.env.REACT_APP_API_ENDPOINT}/v1/staff`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(formData)
            })
            if (network.ok) {
                navigate("/admin/staffs")
                showToast("Staff Added", "success")
            } else {
                const errorText = await network.text();
                showToast(errorText, "error")
            }
        } catch (error) {
            showToast(error.message, "error")
        } finally {
            showLoading(false)
        }

    };

    return (
        <>
            <div className="bg-base-300 shadow-lg rounded-lg p-6 m-16">
                <form
                    className=""
                    onSubmit={handleSubmit}
                >
                    <h2 className="text-2xl font-bold mb-4">Staff Information Form</h2>


                    <div className="flex flex-wrap">
                        <div className="w-1/2 p-4">
                            {/* Staff Name */}
                            <div className="mb-4">
                                <label className="label">
                                    <span className="label-text">Staff Name</span>
                                </label>
                                <input
                                    type="text"
                                    required
                                    name="staff_name"
                                    value={formData.staff_name}
                                    onChange={handleChange}
                                    placeholder="Enter staff name"
                                    className="input input-bordered w-full"
                                />
                            </div>

                            {/* Phone */}
                            <div className="mb-4">
                                <label className="label">
                                    <span className="label-text">Phone</span>
                                </label>
                                <input
                                    type="text"
                                    required
                                    name="staff_phone"
                                    value={formData.staff_phone}
                                    onChange={handleChange}
                                    placeholder="Enter phone number"
                                    className="input input-bordered w-full"
                                />
                            </div>

                            {/* Email */}
                            <div className="mb-4">
                                <label className="label">
                                    <span className="label-text">Email</span>
                                </label>
                                <input
                                    type="email"
                                    required
                                    name="staff_email"
                                    value={formData.staff_email}
                                    onChange={handleChange}
                                    placeholder="Enter email"
                                    className="input input-bordered w-full"
                                />
                            </div>

                            {/* Gender */}
                            <div className="mb-4">
                                <label className="label">
                                    <span className="label-text">Gender</span>
                                </label>
                                <select
                                    name="staff_gender"
                                    required
                                    value={formData.staff_gender}
                                    onChange={handleChange}
                                    className="select select-bordered w-full"
                                >
                                    <option value="male">Male</option>
                                    <option value="female">Female</option>
                                    <option value="other">Other</option>
                                </select>
                            </div>

                            {/* Role */}
                            <div className="mb-4">
                                <label className="label">
                                    <span className="label-text">Role</span>
                                </label>
                                <select
                                    name="role"
                                    required
                                    value={formData.role}
                                    onChange={handleChange}
                                    className="select select-bordered w-full"
                                >
                                    <option value="teacher">Teacher</option>
                                    <option value="admin">Admin</option>
                                    <option value="cashier">Cashier</option>
                                    <option value="staff">Staff</option>
                                </select>
                            </div>

                        </div>

                        <div className="w-1/2 p-4">

                            {/* Hire Date */}
                            <div className="mb-4">
                                <label className="label">
                                    <span className="label-text">Hire Date</span>
                                </label>
                                <input
                                    type="date"
                                    required
                                    name="hire_date"
                                    value={formData.hire_date}
                                    onChange={handleChange}
                                    className="input input-bordered w-full"
                                />
                            </div>

                            {/* Salary */}
                            <div className="mb-4">
                                <label className="label">
                                    <span className="label-text">Salary</span>
                                </label>
                                <input
                                    type="number"
                                    required
                                    name="salary"
                                    value={formData.salary}
                                    onChange={handleChange}
                                    placeholder="Enter salary"
                                    className="input input-bordered w-full"
                                />
                            </div>

                            {/* Bank Name */}
                            <div className="mb-4">
                                <label className="label">
                                    <span className="label-text">Bank Name</span>
                                </label>
                                <input
                                    type="text"
                                    name="bank_name"

                                    value={formData.bank_name}
                                    onChange={handleChange}
                                    placeholder="Enter bank name"
                                    className="input input-bordered w-full"
                                />
                            </div>

                            {/* Account Number */}
                            <div className="mb-4">
                                <label className="label">
                                    <span className="label-text">Account Number</span>
                                </label>
                                <input
                                    type="text"
                                    name="account_no"
                                    value={formData.account_no}
                                    onChange={handleChange}
                                    placeholder="Enter account number"
                                    className="input input-bordered w-full"
                                />
                            </div>

                            {/* IFSC Code */}
                            <div className="mb-4">
                                <label className="label">
                                    <span className="label-text">IFSC Code</span>
                                </label>
                                <input
                                    type="text"
                                    name="ifsc_code"
                                    value={formData.ifsc_code}
                                    onChange={handleChange}
                                    placeholder="Enter IFSC code"
                                    className="input input-bordered w-full"
                                />
                            </div>
                        </div>
                    </div>

                    {/* Submit Button */}
                    <div className="text-center">
                        <button className="btn btn-primary">Submit</button>
                    </div>
                </form>
            </div>
        </>
    );
};

export default StaffForm;
